import { Notification } from 'element-ui'
import store from '../store/index'
import { PARAMS } from "./params"
import { OBJ, ns } from './init'
var ros

/**
 * AGV数据传输到服务器
 * @param {*} topicName 
 * @param {*} message 
 */
export function agvToServerFn(topicName, message){
    OBJ.wsTopic[topicName].publish(message);
}

export function serverRosInit() {
    return new Promise(function (resolve, reject) {
        var ip = window.localStorage.getItem('serIp')
        ros = new ROSLIB.Ros({
            url: 'ws://' + ip,
        });

        //链接到websocket服务器
        ros.on('connection', () => {
            console.log(`[INFO]Connected to rosbridge ${ip}.`);
            Notification({
                title: "成功",
                message: "服务器 " + ip + " 连接成功",
                type: 'ok',
                position: 'top-right',
                offset: 100
            });
            store.commit("setServerContented", '1');
            PARAMS.serConnecting = true;
            clearTimeout(PARAMS.reconnectSettimeout);
            resolve('ros Connected');
            initTopic();
        });
        //关闭websocket服务器
        ros.on('close', () => {
            console.log(`[INFO]Rosbridge server ${ip} closed.`);
            Notification({
                title: "错误",
                message: "服务器连接已关闭",
                type: 'error',
                position: 'top-right',
                offset: 100
            });            
            store.commit("setServerContented", '');
            reject('服务器连接已关闭');
            PARAMS.serConnecting = false;
            reconnect()
        });
        //关闭websocket服务器出错
        ros.on('error', () => {
            Notification({
                title: "错误",
                message: "服务器连接失败",
                type: 'error',
                position: 'top-right',
                offset: 100
            });
            console.log("[INFO] rosbridge error");
            store.commit("setServerContented", '');
            reject('服务器连接失败');
            PARAMS.serConnecting = false;
            reconnect()
        });
    })
}

//检测重连
function reconnect () {
    PARAMS.serConnectFlg = true;
    if (PARAMS.serConnecting) {
        return
    }
    PARAMS.reconnectSettimeout && clearTimeout(PARAMS.reconnectSettimeout)
    PARAMS.reconnectSettimeout = setTimeout(() => {
        serverRosInit()
    }, PARAMS.timeout)
}

function initTopic(){
    return new Promise(function (resolve, reject){
       let list = JSON.parse(window.localStorage.getItem('wsTopicConfig'))
       list.forEach(item => {
        let topicName = item.name;
        OBJ.wsTopic[topicName] = new ROSLIB.Topic({
            ros: ros,
            name: ns + item.name,
            messageType: item.type
        });
        if(item.subFlag == 'pub'){
            OBJ.wsTopic[topicName].subscribe(function(msg){
                OBJ.topic[topicName].publish(msg)
            })
        }
       });
    })
}


