<template>
    <div class="setting">
        <div class="setItem flex heardTop" @click="setAuthorization">
            <div class="title"><i class="el-icon-user setIcon authorIcon"></i>设置权限</div>
            <div class="rightIcon"><i class="el-icon-arrow-right"></i></div>
        </div>

        <div class="collpaseBox">
            <el-collapse accordion>
                <el-collapse-item>
                    <template slot="title">
                        <i class="header-icon el-icon-sort setIcon branchIcon"></i>切换分支
                    </template>
                    <div class="branchSelectBox flex">
                        <el-select class="WebSocketInput" v-model="$store.state.nowMapName" :disabled="!develop" size="medium " placeholder="请选择" popper-class="branchSelect">
                            <el-option
                            v-for="(item, index) in $store.state.mapList"
                            :key="index"
                            :label="item"
                            :value="item"
                            :disabled="item === $store.state.nowMapName"
                            >
                            <span class="leftName" style="float: left" @click="chooseBranch(item)">{{ item }}</span>
                            <span class="rightDel" style="float: right; color: #8492a6; font-size: 13px" @click="delBranch(item)" v-if="item !== 'master'"><i class="el-icon-delete"></i></span>
                            <span class="rightDel" style="float: right; color: #8492a6; font-size: 13px" v-else></span>
                            </el-option>
                        </el-select>
                        <div class="addBranch addWebSocket">
                            <el-tooltip class="item" effect="dark" content="新建分支" placement="bottom">
                                <el-button icon="el-icon-plus" type="primary" size="small" @click="newBranchBtn"></el-button>
                            </el-tooltip>
                        </div>
                    </div>
                </el-collapse-item>
            </el-collapse>
        </div>

        <div class="setItem flex" @click="joystick">
            <div class="title"><i class="el-icon-coordinate setIcon joystickIcon"> </i>操纵杆</div>
            <div class="rightIcon"><i class="el-icon-arrow-right"></i></div>
        </div>

        <div class="setItem flex" @click="wifiSetting">
            <div class="title"><i class="el-icon-s-data setIcon wifiIcon"></i>WIFI设置</div>
            <div class="rightIcon"><i class="el-icon-arrow-right"></i></div>
        </div>

        <div class="setItem flex" @click="initialPose">
            <div class="title"><i class="el-icon-thumb setIcon initialIcon"></i>手动定位</div>
            <div class="rightIcon"><i class="el-icon-arrow-right"></i></div>
        </div>
        <div class="setItem flex" @click="sendGoal">
            <div class="title"><i class="el-icon-thumb setIcon initialIcon"></i>发送目标位置</div>
            <div class="rightIcon"><i class="el-icon-arrow-right"></i></div>
        </div>
        <div class="setItem flex">
            <div class="title"><i class="el-icon-magic-stick setIcon slamIcon"></i>激光显示</div>
            <div class="rightIcon">
                <el-switch
                v-model="slamSwitcn"
                active-color="#409EFF"
                inactive-color="#C0CCDA"
                @change="changeSlam"
                >
                </el-switch>
            </div>
        </div>

        <div class="collpaseBox">
            <el-collapse accordion>
                <el-collapse-item>
                    <template slot="title">
                        <i class="header-icon el-icon-guide setIcon slamIcon"></i>WebSocket
                    </template>
                    <div class="branchSelectBox flex">
                        <wsTopicConfig></wsTopicConfig>
                        <el-input class="WebSocketInput" 
                        placeholder="请输入需要连接的IP和端口号"
                        v-model="ipText" 
                        clearable>
                        </el-input>
                        <div class="addWebSocket">
                            <el-switch
                            v-model="WebSocketSwitcn"
                            active-color="#409EFF"
                            inactive-color="#C0CCDA"
                            @change="changeWebSocket"
                            >
                            </el-switch>
                        </div>
                    </div>
                </el-collapse-item>
            </el-collapse>
        </div>


        <div class="collpaseBox">
            <el-collapse accordion>
                <el-collapse-item>
                    <template slot="title">
                        <i class="header-icon el-icon-bell setIcon volumeIcon"></i>音量调节
                    </template>
                    <div class="flex volumeBox">
                        <div class="modulContent">
                            <el-slider v-model="voice" :format-tooltip="formatTooltip"  @change="changeVoice"></el-slider>
                        </div>
                    </div>
                </el-collapse-item>
            </el-collapse>
        </div>

        <div class="setItem flex" @click="updateSystem">
            <div class="title"><i class="el-icon-refresh setIcon updateIcon"></i>系统更新</div>
            <div class="rightIcon"><i class="el-icon-arrow-right"></i></div>
        </div>

        <div class="setItem flex" @click="clearCache">
            <div class="title"><i class="el-icon-odometer setIcon cacheIcon"></i>清除缓存</div>
            <div class="rightIcon"><i class="el-icon-arrow-right"></i></div>
        </div>

        <div class="setItem flex helpBot" @click="helpBtn">
            <div class="title"><i class="el-icon-headset setIcon helpIcon"></i>帮助</div>
            <div class="rightIcon"><i class="el-icon-arrow-right"></i></div>
        </div>

        
        <el-dialog
        title="提示"
        :visible.sync="cacheDialog"
        width="79%"
        top="30vh"
        center>
            <span>是否清除浏览器缓存?</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="cacheDialog = false">取 消</el-button>
                <el-button type="primary" @click="clearCacheBtn">确 定</el-button>
            </span>
        </el-dialog>
    </div>  
</template>

<script>
import wsTopicConfig from "@/components/pages/modules/wsTopicConfig.vue"
import { adjustVoice } from '@/js/volume';
import { showAuthor, displayLaserScan } from '@/js/methods'
import { switchBranch, deleteBranch } from '@/js/branch';
import { showWifi } from '@/js/wifi'
import { showUpdate } from '@/js/update';
import { showNewMap, showUploadMap, showDownloadMap, displayMap, downMapPng } from '@/js/map'
import { serverRosInit } from '@/js/websocket' 
import { Notification } from 'element-ui'

export default {
    name: 'setting',
    components:{
       wsTopicConfig
    },
    data(){
        return{
            voice: 0,
            slamSwitcn: false,
            cacheDialog: false,
            develop: false,
            ipText: '',
            WebSocketSwitcn: false,
            mapNameList: [{
                value: 'mapTopic',
                label: 'map'
            }, {
                value: 'mapOstreamPathTopic',
                label: 'map_path'
            }, {
                value: 'mapOstreamDisplayTopic',
                label: 'map_display'
            }, {
                value: 'mapOstreamReflectionTopic',
                label: 'map_reflection'
            }]
        }
    },

    //计算属性
    watch:{
        '$store.state.userRole':function(val){
          if(val === "developer"){
              this.develop = true;
          }else {
              this.develop = false;
          }
        },

        '$store.state.serverContented':function(val){
            if(val === ""){
                this.WebSocketSwitcn = false;
            }
        }
    },

    mounted(){
        this.ipText = window.localStorage.getItem('serIp')
    },

    methods:{
        //设置权限
        setAuthorization(){
            showAuthor(true)
        },

        chooseMap(item){
            displayMap(item.value, item.label)
        },

        //切换分支
        chooseBranch: function(name){
            switchBranch(name)
        },

        //删除分支
        delBranch: function(name){
            const that = this;
            that.$confirm(`是否永久删除${name}分支?`, '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
                center: true,
                customClass: "delBranchCustom"
            }).then(() => {
                deleteBranch(name);
            }).catch(() => {
                          
            });
        },

        //新建分支
        newBranchBtn: function(){
            this.$store.commit("switchBranch", true);
        },

        //wifi设置
        wifiSetting: function(){
            showWifi(true);
        },

        //手动定位
        initialPose(){
            this.$emit("transferSetting", "initialPose")
        },
        //手动下发位置
        sendGoal(){
            this.$emit("transferSetting", "goal")
        },

        //操纵杆
        joystick(){
            this.$emit("transferSetting", "joystick")
        },

        //激光开关
        changeSlam(val){
            displayLaserScan(val);
            // this.$emit("transferSetting", "laserScan")
        },

        //webSocket开关
        changeWebSocket(){
            if(this.ipText){
                window.localStorage.setItem('serIp', this.ipText)
                serverRosInit();
            }else{
                this.WebSocketSwitcn = false
                Notification({
                    title: "错误",
                    message: "请输入需要连接的IP地址和端口号",
                    type: 'error',
                    position: 'top-left',
                    offset: 100
                });
                return
            }
        },

        //系统更新
        updateSystem(){
            showUpdate(true);
        },

        //清除缓存
        clearCache(){
            this.cacheDialog = true;
        },

        clearCacheBtn(){
            const that = this;
            const loading = that.$loading({
                lock: true,
                text: '清除中',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.7)'
            });
            window.localStorage.clear();
            that.cacheDialog = false;
            setTimeout(() => {
                that.$notify({
                    message: '清除成功',
                    position: 'top-left',
                    type: "success",
                    offset: 100,
                });
                loading.close();
            }, 500);
        },

        //help
        helpBtn(){
            window.location.href = 'http://hitrobotgroup.github.io/'
        },
        //音量调节
        formatTooltip(val) {
            return val *2;
        },

        changeVoice(val){
            adjustVoice(val*2)
        },
    }
}
</script>

<style scoped lang="less">
    .authorIcon{
        color: #409EFF;
    }
    .mapIcon{
        color: #67C23A;
    }
    .branchIcon{
        color: #FFCC99
    }
    .joystickIcon{
        color: #006699
    }
    .wifiIcon{
        color: #00CC66
    }
    .initialIcon{
        color: #333399
    }
    .slamIcon{
        color: #F56C6C;
    }
    .volumeIcon{
        color: #E6A23C;
    }
    .updateIcon{
        color: #33CCFF
    }
    .cacheIcon{
        color: #6600CC
    }
    .helpIcon{
        color: #CC6699
    }

    .setting{
        position: relative;
        font-size: 14px;
        height: 100%;
        overflow-y: scroll;
        font-size: 16px;
        background-color: #eeeeee;
        .heardTop{
            margin-top: 3px;
        }
        .setItem{
            background-color: #ffffff;
            justify-content: space-between;
            height: 48px;
            line-height: 48px;
            margin-bottom: 3px;
            padding: 0 10px;
            .title{
                flex: 8;
                text-align: left;
            }
            .rightIcon{
                flex: 2;
                text-align: right;
            }
        }
        .collpaseBox{
            margin-bottom: 3px;
            .ws_topic_config{
                padding-top: 16.5px;
                padding-right: 30px;
            }
            }
        .moreBox{
            width: 100%;
            flex-wrap: wrap;
            justify-content: flex-start;
            align-items: flex-start;
            .moreItem{
                font-size: 12px;
                margin: 5px 0px;
                width: 33.333%;
                text-align: center;
                flex-direction: column;
                .icon{
                    width: 50px;
                    height: 50px;
                    border-radius: 4px;
                    background-position: center;
                    background-repeat: no-repeat;
                    background-size: 40%;
                    margin-bottom: 5px;
                }
                .mapNor{
                    background-image: url("../../../src/assets/icon-map-nor.png");
                }
                
                
            }
            .itemTitle{
                width: 61px;
                overflow: hidden;
                white-space: nowrap;
            }
            .boxShadow{
                font-size: 24px;
                color: #616161;
            }
            
        }
        .modulContent{
            width: 100%;
            color: #666666;
            margin-top: 10px;
            padding: 0 15px 0 23px;
        }
        .setIcon{
            margin-right: 10px;
            font-size: 20px;
        }
        .branchSelectBox{
            .WebSocketInput{
                width: 55%;
                margin-top: 15px;
            }
            .addBranch{
                margin-left: 15px;
            }
            .addWebSocket{
                margin-left: 30px;
                margin-top: 15px;
            }
        }
        .helpBot{
            margin-bottom: 20px;
        }
    }
</style>


