export let PARAMS = {
    NavCtrl: {
        stop: 0,
        start: 1,
        pause: 2
    },
    NavCtrlStatus: {
        error: -1,//故障
        idling: 0,//可执行
        running: 1,//执行中
        paused: 2,//暂停
        completed: 3,//已完成
        cancelled: 4//任务已取消
    },
    rosMode: {
        navigation: "navigation",
        gmapping: "gmapping"
    },
    NetworkMode: {
        ap: 'ap',
        wifi: 'wifi'
    },
    gitMethods: {
        add: "add",
        remove: "remove",
        select: "select",
        query: "query"
    },
    POSE:{
        poseStartPoint: null,
        editContainer: null,
        poseStartPointShape: null
    },
    butteryStatus: {
        charging: "charging",
        success: "success",
        warning: "warning",
        timeout: 60000,//电量提示时间间隔
        timeoutObj: null,
        chargingFlag: false
    },
    joystickObj: {
        up: false,
        down: false,
        left: false,
        right: false,
        upTimeout: null,
        downTimeout: null,
        leftTimeout: null,
        rightTimeout: null,
        upTime: 0,
        downTime: 0,
        leftTime: 0,
        rightTime: 0,
        maxSpeed: 0.4
    },
    connecting: false,//正在连接
    connectFlg: false,
    serConnecting: false,//服务器正在连接
    serConnectFlg: false,
    reconnectSettimeout: null, // 重新连接的setTimeout
    timeout: 3000 // 心跳监测间隔
}


