<template>
    <el-drawer
    title="添加站点"
    :visible.sync="drawer"
    :close-on-press-escape="false"
    custom-class="authorBox"
    :wrapperClosable="true"
    :show-close="showClose"
    :direction="direction"
    @close="closeDrawer">
        <el-form ref="ruleForm"  class="waypointForm">
            <el-form-item class="">
                <div class="flex formItem">
                    <div class="itemLeft">站点名称</div>
                    <div class="itemRight"><input type="text" placeholder="请输入站点名称" v-model="stationForm.stationName"></div>
                </div>
            </el-form-item>

            <el-form-item>
                <div class="flex formItem">
                    <div class="itemLeft">站点类型</div>
                    <div class="itemRight">
                        <el-select v-model="stationForm.type" placeholder="请选择站点类型" @change="changeStationType" size="mini" class="stationType" popper-class="selectClass">
                            <el-option
                            v-for="item in stationForm.stationType"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                    </div>
                </div>
            </el-form-item>

            <div class="showMore" v-show="showMore" @click="showMoreBtn">更多设置<i class="el-icon-d-arrow-right moreIcon"></i></div>
            <div v-show="!showMore">
                <!-- 默认站点模式 -->
                <el-form-item class="" v-show="showStationMode">
                    <div class="flex formItem">
                        <div class="itemLeft">站点模式</div>
                        <div class="itemRight">
                            <el-select v-model="stationForm.mode" placeholder="请选择站点模式" size="mini" class="stationType" popper-class="selectClass">
                                <el-option
                                v-for="item in stationForm.stationMode"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                                </el-option>
                            </el-select>
                        </div>
                    </div>
                </el-form-item>

                <!-- 站点列表模式 -->
                <el-form-item class="" v-show="showGotoStationMode">
                    <div class="flex formItem">
                        <div class="itemLeft">站点模式</div>
                        <div class="itemRight">
                            <el-select v-model="stationForm.gotoMode" placeholder="请选择站点模式" size="mini" class="stationType">
                                <el-option
                                v-for="item in this.$store.state.stationList"
                                :key="item.name"
                                :label="item.name"
                                :value="item.name">
                                </el-option>
                            </el-select>
                        </div>
                    </div>
                </el-form-item>

                <!-- call列表模式 -->
                <el-form-item class="" v-show="showCallStationMode">
                    <div class="flex formItem">
                        <div class="itemLeft">站点模式</div>
                        <div class="itemRight">
                            <el-select v-model="stationForm.callMode" placeholder="请选择站点模式" size="mini" class="stationType">
                                <el-option
                                v-for="item in this.$store.state.trajectoryList"
                                :key="item.name"
                                :label="item.name"
                                :value="item.name">
                                </el-option>
                            </el-select>
                        </div>
                    </div>
                </el-form-item>

                <!-- 声音站点模式 -->
                <el-form-item class="" v-show="showVoiceStationMode">
                    <div class="flex formItem">
                        <div class="itemLeft">站点模式</div>
                        <div class="itemRight">
                            <el-select v-model="stationForm.voiceMode" placeholder="请选择站点模式" size="mini" class="stationType">
                                <el-option
                                v-for="item in stationForm.voiceStationMode"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                                </el-option>
                            </el-select>
                        </div>
                    </div>
                </el-form-item>

                <el-form-item class="" v-show="showFrameId">
                    <div class="flex formItem">
                        <div class="itemLeft">frame_id</div>
                        <div class="itemRight"><input type="text" placeholder="请输入frame_id" v-model="stationForm.frameId"></div>
                    </div>
                </el-form-item>

                <el-form-item class="" v-show="showSubscribeValue">
                    <div class="flex formItem">
                        <div class="itemLeft">发布-订阅器接收值</div>
                        <div class="itemRight"><input type="text" placeholder="请输入接收值" v-model="stationForm.subscribeValue"></div>
                    </div>
                </el-form-item>

                <!-- <el-form-item class="">
                    <div class="flex formItem">
                        <div class="itemLeft">范围</div>
                        <div class="itemRight"><input type="text" placeholder="请输入站点范围" v-model="stationForm.scope"></div>
                    </div>
                </el-form-item> -->

                <!-- <el-form-item class="">
                    <div class="flex formItem">
                        <div class="itemLeft">超时时间</div>
                        <div class="itemRight"><input type="text" placeholder="请输入超时时间" v-model="stationForm.timeout"></div>
                    </div>
                </el-form-item> -->

                <el-form-item class="" v-show="showPosition">
                    <div class="localTitle">Positon</div>
                    <div class="flex formItem bottomNone">
                        <div class="localInputItem">
                            <div class="itemLeft">x</div>
                            <div class="itemRight">
                                <input type="text" placeholder="" v-model="stationForm.positionX">
                            </div>
                        </div>

                        <div class="localInputItem">
                            <div class="itemLeft">y</div>
                            <div class="itemRight">
                                <input type="text" placeholder="" v-model="stationForm.positionY">
                            </div>
                        </div>

                        <div class="localInputItem">
                            <div class="itemLeft">z</div>
                            <div class="itemRight">
                                <input type="text" placeholder="" v-model="stationForm.positionZ">
                            </div>
                        </div>
                    </div>
                </el-form-item>

                <el-form-item class="" v-show="showOrientation">
                    <div class="localTitle">Orientation</div>
                    <div class="flex formItem bottomNone">
                        <div class="localInputItem">
                            <div class="itemLeft">x</div>
                            <div class="itemRight">
                                <input type="text" placeholder="" v-model="stationForm.orientationX"  @change="changeOrientation">
                            </div>
                        </div>
                        <div class="localInputItem">
                            <div class="itemLeft">y</div>
                            <div class="itemRight">
                                <input type="text" placeholder="" v-model="stationForm.orientationY"  @change="changeOrientation">
                            </div>
                        </div>
                        <div class="localInputItem">
                            <div class="itemLeft">z</div>
                            <div class="itemRight">
                                <input type="text" placeholder="" v-model="stationForm.orientationZ"  @change="changeOrientation">
                            </div>
                        </div>
                        <div class="localInputItem">
                            <div class="itemLeft">w</div>
                            <div class="itemRight">
                                <input type="text" placeholder="" v-model="stationForm.orientationW"  @change="changeOrientation">
                            </div>
                        </div>
                    </div>
                </el-form-item>
    
                <el-form-item class="" v-show="showYaw">
                    <div class="flex formItem">
                        <div class="itemLeft">Yaw(deg)</div>
                        <div class="itemRight"><input type="text" placeholder="请输入Yaw" v-model="stationForm.yaw" @change="changeYaw" @keyup.enter="subUpdate('stationForm')"></div>
                    </div>
                </el-form-item>
            </div>
            <div class="flex formBtns">
                <el-button type="primary" @click="subUpdate('stationForm')">提交</el-button>
            </div>
        </el-form>
    </el-drawer>
</template>

<script>
import { isQuaternionValid, quaternionToYaw, thetaToQuaternion } from '@/js/methods'
import { showWaypoint, submitAddWaypoint, getRobotPose } from '@/js/waypoint'
export default {
    name: 'stationAdd',
    components:{
        
    },
    data(){
        return{
            drawer: false,
            direction: 'btt',
            showClose: false,

            name: "waypoint",

            showMore: true,
            stationForm:{
                stationName:'',
                stationType: [{
                    value: 'goal',
                    label: "目标站点"
                }, {
                    value: 'dock',
                    label: "充电桩"
                }, {
                    value: 'timer',
                    label: "计时器"
                }, {
                    value: 'publisher',
                    label: "发布器"
                }, {
                    value: 'subscriber',
                    label: "订阅器"
                }, {
                    value: 'looper',
                    label: "循环"
                }, {
                    value: 'pubsuber',
                    label: "发布-订阅器"
                }, {
                    value: 'cmd_vel_set_sub',
                    label: "最大速度设置"
                }, {
                    value: 'cmd_vel',
                    label: "速度发布器"
                }, {
                    value: 'shell',
                    label: "命令发布器"
                }, {
                    value: 'sound_play',
                    label: "声音播放"
                }, {
                    value: 'initial_pose',
                    label: "初始位置"
                }, {
                    value: 'scan_marker',
                    label: "充电桩定位"
                }, {
                    value: 'shell_string',
                    label: "Shell命令"
                }, {
                    value: 'pallet',
                    label: 'Pallet'
                }, {
                    value: 'bias',
                    label: 'Bias'
                }, {
                    value: 'call',
                    label: 'Call'
                }, {
                    value: 'goto',
                    label: 'Goto'
                }],
                type: "goal",
                stationMode:[{
                    value: 'LOOP',
                    label: 'LOOP',
                }, {
                    value: 'LOOP2',
                    label: 'LOOP2'
                }, {
                    value: 'BACK',
                    label: 'BACK'
                }, {
                    value: 'BACK2',
                    label: 'BACK2'
                }, {
                    value: 'NONE',
                    label: "单次",
                }],
                mode: 'LOOP',
                gotoMode: '',
                callMode: '',
                voiceMode: 'play',
                voiceStationMode:[{
                    value: 'LOOP',
                    label: "播放",
                }, {
                    value: 'LOOP2',
                    label: "停止",
                }, {
                    value: 'BACK',
                    label: "播放一次",
                },],
                frameId:'map',
                subscribeValue:'0',
                positionX: 0.0,
                positionY: 0.0,
                positionZ: 0.0,
                orientationX: 0.0,
                orientationY: 0.0,
                orientationZ: 0.0,
                orientationW: 0.0,
                yaw: 0.0,
                // scope: 0.0,
                // timeout: 0.0
            },
            showFrameId: true,
            showSubscribeValue: false,
            showStationMode: true,
            showVoiceStationMode: false,
            showGotoStationMode: false,
            showCallStationMode: false,
            showPosition: true,
            showOrientation: true,
            showYaw:true
        }
    },
    mounted(){
        
    },
    methods:{
        showMoreBtn:function(){
            var getPose = getRobotPose();
            if(getPose){
                this.showMore = false;
            }
        },

        //选择站点类型
        changeStationType(type){
            let that = this;
            that.showFrameId = false;
            that.showSubscribeValue = false;
            that.showStationMode = true,
            that.showVoiceStationMode = false;
            that.showGotoStationMode = false;
            that.showCallStationMode = false;
            that.showPosition = true;
            that.showOrientation = true;
            that.showYaw = true;
            switch(type){
                case 'goal':
                that.showFrameId = true;
                break;

                case 'pubsuber':
                that.showSubscribeValue = true;
                break;

                case 'sound_play':
                that.showStationMode = false;
                that.showVoiceStationMode = true;
                that.showGotoStationMode = false;
                that.showCallStationMode = false;
                break;

                case 'goto':
                that.showStationMode = false;
                that.showVoiceStationMode = false;
                that.showGotoStationMode = true;
                that.showCallStationMode = false;
                break;

                case 'call':
                that.showStationMode = false;
                that.showVoiceStationMode = false;
                that.showGotoStationMode = false;
                that.showCallStationMode = true;
                break;

                case 'initial_pose':
                that.showFrameId = true;
                break;

                default:
                
                break;
            }
        },

        //改变orientation
        changeOrientation(){
            let that = this;
            let orientation = {
                x: parseFloat(this.stationForm.orientationX),
                y: parseFloat(this.stationForm.orientationY),
                z: parseFloat(this.stationForm.orientationZ),
                w: parseFloat(this.stationForm.orientationW),
            }
            //有效的四元数转换
            if (isQuaternionValid(orientation)){
                let yawRad = quaternionToYaw(orientation);
                let yawDeg = yawRad * 180 / Math.PI;
                that.stationForm.yaw = yawDeg.toFixed(5);
            }else{
                // that.$message({
                //     showClose: true,
                //     message: '四元数不合法',
                //     type: 'warning'
                // });
                this.$notify({
                    title: "警告",
                    message: "四元数不合法",
                    type: 'warning',
                    position: 'top-left',
                    offset: 100
                });
                return
            }
        },

        /**
         * 改变yaw,转换为四元数
         * **/
        changeYaw(){
            let that = this;
            let yawDeg = parseFloat(that.stationForm.yaw);
            let orientation = thetaToQuaternion(yawDeg/180*Math.PI);
            that.stationForm.orientationX = orientation.x.toFixed(5);
            that.stationForm.orientationY = orientation.y.toFixed(5);
            that.stationForm.orientationZ = orientation.z.toFixed(5);
            that.stationForm.orientationW = orientation.w.toFixed(5);
        },

        //提交
        subUpdate() {
            if(this.stationForm.type === "goal"){
                this.stationForm.type = 'goal'
            }
            if(this.stationForm.mode === 'LOOP'){
                this.stationForm.mode = 'LOOP'
            }
            if(this.stationForm.voiceMode === 'play'){
                this.stationForm.voiceMode = 'LOOP'
            }
            submitAddWaypoint(this.stationForm);
      },

      //关闭模态框
      closeDrawer: function(){
          showWaypoint(false)
      }
    },

    //计算属性,小车姿态转换
    watch:{
       '$store.state.robotPosition':function(val){
            this.stationForm.positionX = val.positionX;
            this.stationForm.positionY = val.positionY;
            this.stationForm.positionZ = val.positionZ;
            this.stationForm.orientationX = val.orientationX;
            this.stationForm.orientationY = val.orientationY;
            this.stationForm.orientationZ = val.orientationZ;
            this.stationForm.orientationW = val.orientationW;
            this.stationForm.yaw = val.yaw;
        },

        "$store.state.showAddStation": function(val){
           this.drawer = val;
        }        
    }
}
</script>

<style scoped lang="less">
.waypointForm{
    padding: 0 15px;
    .formItem{
        border-bottom: 1px solid #eeeeee;
        .itemLeft{
            flex: 4;
            text-align: left;
        }
        .itemRight{
            flex: 6;
            height: 40px;
            input{
                width: 100%;
                height: 100%;
                display: block;            
            }
            .selectClass{
                display: flex;
                justify-content: center;
                align-items: center
            }
        }
    }
    .bottomNone{
        border-bottom: none;
    }
    .showMore{
        text-align: center;
        color: #409EFF;
        padding-top: 5px;
        .moreIcon{
            transform:rotate(90deg);
            -ms-transform:rotate(90deg);
            -moz-transform:rotate(90deg);
            -webkit-transform:rotate(90deg);
            -o-transform:rotate(90deg);
        }
    }
     .localTitle{
        text-align: left;
        padding: 10px 0 0 0;
    }
    .localInputItem{
        text-align: center;
        padding: 0 5px;
        .itemLeft{
            text-align: center;
        }
        input{
            border: 1px dashed #eeeeee;
            padding: 5px 0;
            text-align: center;
            margin-top: 5px;
        }
    }
    .stationType{
        input{
            height: 10px;
        }
        .el-input__icon{
            height:inherit
        }
        .el-input__inner{
            height: normal;
            line-height: normal
        }
        width: 100%;
        border: none;
        outline: none;
        padding-left: 0.7em;
        height: 100%;
    }
     .formBtns{
        padding: 0 5px;
        margin-top: 15px;
        button{
            width: 100%;
        }
    }
    .el-form-item{
        margin-bottom: 0;
    }
    .el-form-item__content{
        line-height: 20px;
    }
}
</style>


