<template>
  <div>
    <el-drawer
      title="地图"
      :visible.sync="drawer"
      :close-on-press-escape="false"
      custom-class="mappingBox"
      :wrapperClosable="closable"
      :show-close="showClose"
      :direction="direction"
      @close="closeDrawer"
      >
      
      <div class="authorCom">
        <div class="editItem flex">
          <el-button native-type="button" class="editMapBtn" @click="newMappingBtn">新建地图</el-button>
        </div>
        <div class="editItem flex">
          <el-button native-type="button" class="editMapBtn" @click="continueMappingBtn">继续建图</el-button>
        </div>
      </div>
    </el-drawer>

    <div class="mappingSaveClose flex" v-show="mappingEnd">
      <el-button type="primary" icon="el-icon-check" circle @click="saveMapContinue"></el-button>
      <el-button type="warning" icon="el-icon-close" circle @click="saveMapClose"></el-button>
    </div>
  </div>
</template>

<script>
import { displayMap, newMapping, continueMapping, showNewMap, submitSaveMapContinue, submitSaveMapBtn } from "@/js/map";
export default {
  name: "mapping",
  components: {
    
  },
  data() {
    return {
      drawer: false,
      direction: 'btt',
      closable: true,
      showClose: false,
      mapName: null,
      mappingEnd: false,
      name: "map"
    };
  },
  mounted(){
    //判断是否为建图状态来显示 继续和关闭保存 建图按钮
    let mappingStatus = window.localStorage.getItem('mappingStatus');
    if(mappingStatus == '1'){
      this.mappingEnd = true;
    }else{
      this.mappingEnd = false;
    }
  },

  methods: {
    //新建地图
    newMappingBtn: function(){
      this.mappingNotify();
      newMapping();
      displayMap('/map', 'map');
    },

    //继续建图
    continueMappingBtn: function(){
      this.mappingNotify();
      continueMapping();
      displayMap('/map', 'map');
    },

    //建图开始
    mappingNotify: function(){
      const that = this;
      that.$notify({
          title: '提示',
          message: '请缓慢移动机器人，先点击蓝色“保存按钮”，再点击橙色“关闭按钮”',
          type: 'warning',
          position: 'top-left',
          offset: 100,
          duration: 0
      });
      that.mappingEnd = true;
      showNewMap(false);
    },

    //保存并继续建图
    saveMapContinue: function() {
      let that = this;
      if(!that.mapName){
        that.mapNamePrompt("save");
      } else{
        submitSaveMapContinue(this.mapName);
      }
    },

    //点击保存并关闭
    saveMapClose: function() {
      if(!this.mapName){
        this.mapNamePrompt("close")
      } else{
        submitSaveMapBtn(this.mapName);
      }
      this.mappingEnd = false;
    },

    //prompt
    mapNamePrompt: function(type){
      let that = this;
      that.$prompt('请输入地图名称', '提示', {
        customClass: "mappingInputPrompt",
        inputValue: "map",
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /^\w+$/,
        inputErrorMessage: '名称不合法'
      }).then(({ value }) => {
        that.$notify({
          title: '提示',
          message: '建图完成之后建议重启小车',
          type: 'warning',
          position: 'top-left',
          offset: 100,
          duration: 0
        });
        that.mapName = value;
        if(type === "save"){
          submitSaveMapContinue(value);
        } else{
          submitSaveMapBtn(value);
        }
      }).catch(() => {
        
      });
    },

    //关闭模态框
    closeDrawer: function(){
      showNewMap(false);
    }
  },

  //属性监听
  watch:{
      "$store.state.edipMapModule": function(val){
          this.drawer = val;
      }
  }
};
</script>

<style scoped lang="less">
 .skip{
    position: absolute;
    color: #409EFF;
    top: -100px;
    right: 15px;
 }
.authorCom {
  width: 100%;
  padding: 0 15px;
  button {
    background: none;
    color: #ffffff;
  }
  .editItem {
    flex: 1;
    background-color: #409eff;
    color: #ffffff;
    border-radius: 4px;
    height: 35px;
    line-height: 35px;
    margin-top: 20px;
    .editMapBtn {
      width: 100%;
      border: none;
      padding: 0;
    }
  }
  .mapNameInp {
   height: 35px;
    line-height: 35px;
  }
}
.mappingSaveClose{
  position: fixed;
  z-index: 999;
  right: 10px;
  bottom: 340px;
  width: 50px;
  flex-direction: column;
  button{
    margin-left: 0;
    margin-bottom: 20px;
  }
}
</style>


