import vue from '../main'
import store from '../store/index';
import { Notification  } from 'element-ui';
import { OBJ } from './init'
/**
 * 发布FlexbeBehaviorGoal
 * name :需要发布的任务名称
 * **/
export function pubFlexbeBehaviorGoal(name){
    var msg = new ROSLIB.Message({
        header: {
            seq: 0,
            stamp: {
                secs: 0,
                nsecs: 0
            },
            frame_id: ''
        },
        goal_id: {
            stamp: {
                secs: 0,
                nsecs: 0
            },
            id: ''
        },
        goal: {
            behavior_name: name,
            arg_keys: [''],
            arg_values: [''],
            input_keys: [''],
            input_values: ['']
        }
    })
    console.log(msg)
    OBJ.topic['/flexbe/execute_behavior/goal'].publish(msg)
}

/**
 * 取消Flexbe任务
 * **/
export function pubFlexbePreempt(){
    var msg = new ROSLIB.Message({})
    OBJ.topic['/flexbe/command/preempt'].publish(msg)
    Notification({
        title: "成功",
        message: "终止任务成功",
        type: 'success',
        position: 'top-left',
        offset: 100
    });
}

/**
 * 暂停flexbe任务
 * **/
export function pubFlexbePause(){
    var pauseState = window.localStorage.getItem('pauseState')
    if(pauseState){
        if(pauseState == 'true'){
            pauseState = false
            window.localStorage.setItem('pauseState', 'false')
        }else{
            pauseState = true;
            window.localStorage.setItem('pauseState', 'true')
        }
    }else{
        window.localStorage.setItem('pauseState', 'true')
        pauseState = true
    }
    var msg = new ROSLIB.Message({
        data: pauseState
    })
    console.log('pauseState' + pauseState)
    OBJ.topic['/flexbe/command/pause'].publish(msg)
    if(pauseState){
        Notification({
            title: "成功",
            message: "任务已暂停",
            type: 'success',
            position: 'top-left',
            offset: 100
        });
    }else{
        Notification({
            title: "成功",
            message: "任务已恢复",
            type: 'success',
            position: 'top-left',
            offset: 100
        });
    }
    
    return pauseState
}

export function deleteFlexbeTask(name){
    let taskMsg = new ROSLIB.Message({
        seq: 1,
        frame_id: 'flexbe_delete_' + name
    })
    OBJ.topic['/task_switch'].publish(taskMsg);
}


