<template>
    <div class="trajectorys">
        <div class="searchBox">
            <el-input placeholder="请输入轨迹名称" v-model="inputTrajectory" class="input-with-select" @input="InputChange">
            </el-input>
        </div>
        <el-collapse accordion>
            <el-collapse-item v-for="(item,index) in trajectorys" :key="index">
                <template slot="title">
                    <i class="header-icon el-icon-guide trajectoryName"></i>
                    <span class="nameBox">{{item.name}}</span>
                    <span class="desc" v-if="item.name === 'autocharge_on'">开启自动充电</span>
                    <span class="desc" v-if="item.name === 'autocharge_off'">结束自动充电</span>
                </template>
                <div>
                    <trojectoryDetail :details="item"></trojectoryDetail>
                </div>
            </el-collapse-item>
            <el-divider></el-divider>
        </el-collapse>
        <div class="flex searchAddGroup">
            <el-button type="primary" icon="el-icon-plus" circle @click.stop="addBtn"></el-button>
        </div>
    </div>    
</template>

<script>
import trojectoryDetail from "@/components/pages/modules/trajectoryDetail.vue"
import { showTrajectoryList, showTrajectoryAdd } from '../../js/trajectory'
import { debounce } from '../../js/methods'
export default {
    name: 'trajectorys',
    components:{
       trojectoryDetail
    },
    data(){
        return{
            trajectorys: [],
            trajectoryList:[],
            inputTrajectory: ""
        }
    },

    //计算属性 判断带有_display的不显示
    watch:{
        '$store.state.trajectoryList':function(val){
           val.forEach(element => {
            if (element.name.indexOf('_display') < 0 ) {
                this.trajectorys.push(element)
                this.trajectoryList.push(element)
            }
           });
       },
    },

    mounted(){
        
    },

    methods:{
        //搜索轨迹
        InputChange: debounce(function(){
            let that = this;
            console.log("000")
            if(that.inputTrajectory){
                that.trajectorys = [];
                for(var i in that.trajectoryList){
                    if(that.trajectoryList[i].name.indexOf(that.inputTrajectory) >= 0){
                        that.trajectorys.push(that.trajectoryList[i])
                    }
                }
            }else{
                that.trajectorys = that.trajectoryList;
            }
        }, 500),

        addBtn(){
            showTrajectoryList(false);
            showTrajectoryAdd(true);
        }
    }
}
</script>

<style scoped lang="less">
    .trajectorys{
        position: relative;
        font-size: 14px;
        height: 100%;
        font-size: 16px;
        background-color: #eeeeee;
        .trajectoryName{
            color: #409EFF;
        }
        .desc{
            font-size: 12px;
            color: #909399;
            margin-left: 10px;
        }
        .searchBox{
            padding: 15px;
            background-color: #ffffff;
        }
    }
    .searchAddGroup{
        position: fixed;
        flex-direction: column;
        right: 10px;
        bottom: 70px;
        button{
            margin-bottom: 20px;
        }
    }
</style>


