<template>
    <div class="page">
        <div class="amclOrigin" >定位：<svg-icon icon-class="signal" class="amclOriginSvg" /></div>
        <div class="amclReflection">反光板：<svg-icon icon-class="signal" class="amclReflectionSvg"/></div>
        <div v-show="this.$store.state.showBattery"><electricity></electricity></div>
         
        <!-- 保存地图按钮 -->
         <transition name="el-zoom-in-bottom">
            <mapping></mapping>
         </transition>

         <!-- 授权密码 -->
         <transition name="el-zoom-in-bottom">
             <authorization></authorization>
         </transition>

         <!-- 新建分支 -->
         <transition name="el-zoom-in-bottom">
             <add-branch></add-branch>
         </transition>

         <!-- 拖拽圆盘 -->
         <transition name="el-fade-in-center">
             <joystick-component></joystick-component>
         </transition>
    
         <!-- 添加站点 -->
         <transition name="el-zoom-in-bottom">
            <waypoint-add ></waypoint-add>
         </transition>

         <!-- 添加轨迹 -->
         <transition name="el-zoom-in-bottom">
             <trajectory-add></trajectory-add>
         </transition>

         <!-- wifi -->
         <transition name="el-zoom-in-bottom">
             <wifi-setting></wifi-setting>
         </transition>
         
         <!-- 软件更新 -->
         <transition name="el-zoom-in-bottom">
             <update></update>
         </transition>

         <!-- 上传地图 -->
         <transition name="el-zoom-in-bottom">
             <upload-map></upload-map>
         </transition>

         <!-- 下载地图 -->
         <transition name="el-zoom-in-bottom">
             <!-- <download-map></download-map> -->
         </transition>
         
         <el-tabs tab-position="bottom" v-model="tabModel" class="tabs" stretch>
            <el-tab-pane name="map">
                <div slot="label" class="tabItem flex">
                    <i class="el-icon-position"></i>
                    <span>地图</span>
                </div>
                <div>
                    <div id="mapNavDiv"></div>
                    <!-- FlexBe 任务取消停止 -->
                    <div class="boxShadow flexBePauseBtn" v-bind:class="{ clickPause: isClickPause }">
                        <el-button v-bind:icon="iconData" @click="flexBePauseBtn"></el-button>
                    </div>
                    <!-- FlexBe 任务取消停止 -->
                    <div class="boxShadow flexBePreemptBtn">
                        <el-button icon="el-icon-remove-outline" @click="flexBePreemptBtn"></el-button>
                    </div>

                    <!-- 地图图层 -->
                    <div class="boxShadow mapLayer" v-show="develop">
                        <el-button icon="el-icon-copy-document" @click="mapLayer = true"></el-button>
                    </div>

                    <!-- 放大缩小 -->
                    <div class="boxShadow flex mapSizeBtn">
                        <el-button icon="el-icon-plus" :disabled="this.$store.state.zoomDisabled === 0" class="zoomBtn" @click="mapZoom('max')"></el-button>
                        <el-button icon="el-icon-minus" :disabled="this.$store.state.zoomDisabled === 1" class="zoomBtn" @click="mapZoom('min')"></el-button>
                    </div>

                    
                </div>
            </el-tab-pane>
            <!-- 状态显示 -->
            <el-dialog title="诊断信息显示"
            :visible.sync="statusDialog"
            custom-class="statusDialog">
                <el-tree
                :data="statusTable"
                :default-expanded-keys="expandedList"
                node-key="label"
                draggable
                @node-expand="nodeExpand"
                @node-collapse="nodeCollapse"
                :render-content="renderContent">
                </el-tree>
            </el-dialog>
            <el-tab-pane name="oftenFlexbeList">
                <div slot="label" class="tabItem flex">
                    <i class="el-icon-location-information"></i>
                    <span>常用功能</span>
                </div>
                <div>
                    <fblists @transferSetting = "flexbeVueVal" :flexTag = "base" ></fblists>
                </div>
            </el-tab-pane>
            <el-tab-pane name="flexbelist">
                <div slot="label" class="tabItem flex">
                    <i class="el-icon-guide"></i>
                    <span>任务列表</span>
                </div>
                <fblists @transferSetting = "flexbeVueVal" :flexTag = "other"></fblists>
            </el-tab-pane>
            <el-tab-pane label="设置" name="setting">
                <div slot="label" class="tabItem flex">
                    <i class="el-icon-setting"></i>
                    <span>设置</span>
                </div>
                <setting @transferSetting = "settingVal"></setting>
            </el-tab-pane>
        </el-tabs>
        
        <!-- 右面板 显示地图部分 -->
        <el-drawer
        title="地图"
        :visible.sync="mapLayer"
        size="50%"
        :with-header="false"
        custom-class= "mapRight"
        >
            <template>
                <div class="dividingLine">
                    <p>地图</p>
                </div>
                <div class="flex mapBox">
                    <div class="moreItem flex" @click="chooseMap('/map_ostream', 'map')">
                        <div class="icon boxShadow map" ></div>
                        <p class="itemTitle">地图</p>
                    </div>
                    <div class="moreItem flex" @click="chooseMap('/map_ostream_path', 'map_path')">
                        <div class="icon boxShadow mapPath" ></div>
                        <p class="itemTitle">路线地图</p>
                    </div>
                    <div class="moreItem flex" @click="chooseMap('/map_ostream_display', 'map_display')">
                        <div class="icon boxShadow mapDisplay" ></div>
                        <p class="itemTitle">显示地图</p>
                    </div>
                    <div class="moreItem flex" @click="chooseMap('/map_ostream_reflection', 'map_reflection')">
                        <div class="icon boxShadow mapReflection" ></div>
                        <p class="itemTitle">反光板地图</p>
                    </div>
                </div>                
                <!-- 建图 导出 导入 -->
                <div class="dividingLine">
                    <p>选项</p>
                </div>
                <div class="flex moreBox">
                    <div class="moreItem flex">
                        <div class="icon boxShadow mapNor" @click="mappingBtn"></div>
                        <p class="itemTitle">建图</p>
                    </div>
                    <div class="moreItem flex">
                        <div class="icon flex boxShadow" @click="downloadMap">
                            <i class="el-icon-download"></i>
                        </div>
                        <p class="itemTitle">导出</p>
                    </div>
                    <div class="moreItem flex">
                        <div class="icon flex boxShadow" @click="uploadMap">
                            <i class="el-icon-upload2"></i>
                        </div>
                        <p class="itemTitle">导入</p>
                    </div>
                </div>

                <div class="dividingLine">
                    <p>其他</p>
                </div>
                <div class="flex moreBox">
                    <div class="moreItem flex">
                        <div class="icon flex boxShadow">
                            <el-button icon="el-icon-warning" @click="statusDialog = true"></el-button>
                        </div>
                        <p class="itemTitle">诊断信息</p>
                    </div>
                </div>
               
            </template>
        </el-drawer>
    </div>    
</template>

<script>
import electricity from '@/components/pages/modules/electricity.vue'
import mapping from '@/components/pages/modules/mapping.vue'
import authorization from '@/components/pages/modules/authorization.vue'
import addBranch from '@/components/pages/modules/addBranch.vue'
import joystickComponent from '@/components/pages/modules/joystickComponent.vue'
import waypointAdd from '@/components/pages/modules/waypointAdd.vue'
import trajectoryAdd from '@/components/pages/modules/trajectoryAdd.vue'
import wifiSetting from '@/components/pages/modules/wifiSetting.vue'
import update from '@/components/pages/modules/update.vue'
import uploadMap from '@/components/pages/modules/mapUpload.vue'
import waypoints from '@/components/pages/waypoints.vue'
import trajectorys from '@/components/pages/trajectorys.vue'
import fblists from '@/components/pages/flexBeList.vue'
import setting from '@/components/pages/setting.vue'

import { MessageBox } from 'element-ui'
import { displayJoystick } from '@/js/joystick'
import { stopRobot } from '@/js/navigation'
import { locationRobot, poseEstimate, initPoseFn } from '@/js/robot'
import { pubFlexbePreempt, pubFlexbePause } from '@/js/flexbe'
import { zoomIn, zoomOut, showNewMap, showUploadMap, displayMap, downMapPng } from '@/js/map'
import { toTree } from '@/js/methods'

export default {
    name: 'home',
    components:{
        electricity,
        mapping,
        joystickComponent,
        waypointAdd,
        trajectoryAdd,
        wifiSetting,
        authorization,
        addBranch,
        update,
        uploadMap,
        waypoints,
        trajectorys,
        fblists,
        setting
    },
    data(){
        return{
            tabModel: "map",
            statusTable: [], 
            statusDialog: false,
            mapLayer: false,
            radio: '1',
            develop: this.$store.state.userRole === "developer" ? true : false,
            iconData: 'el-icon-video-pause',
            isClickPause: false,
            data: [],
            expandedList: [], // 作为el-tree 的默认展开选项，在 数据刷新之后el-tree 会自动根据这个数据来展开对应选项
            base: 'base',
            other: 'other'
        }
    },

    //计算属性
    watch:{
        //点击站点图标，tabs切换到站点
        "$store.state.wayPointMsg": function(val){
            this.tabModel = "waypoint";
        },

        '$store.state.userRole':function(val){
          if(val === "developer"){
              this.develop = true;
          }else {
              this.develop = false;
          }
        },

        //获取状态集合
        "$store.state.statusArray": function(val){
            this.statusTable = toTree(val);
        },
        
        "$store.state.amclOriginFLag": function(val){
            if(val == 1){
                $('.amclOriginSvg .useIcon').attr('fill', 'LightGrey');
                $('.amclOriginSvg .useIcon2').attr('fill', 'black');
            }else if(val == 0){
                $('.amclOriginSvg .useIcon').attr('fill', 'black');
                $('.amclOriginSvg .useIcon2').attr('fill', 'black');
            }else{
                $('.amclOriginSvg .useIcon').attr('fill', 'LightGrey');
                $('.amclOriginSvg .useIcon2').attr('fill', 'LightGrey');
            }
        },
        "$store.state.amclReflectionFLag": function(val){
            if(val == 1){
                $('.amclReflectionSvg .useIcon').attr('fill', 'LightGrey');
                $('.amclReflectionSvg .useIcon2').attr('fill', 'black');
            }else if(val == 0){
                $('.amclReflectionSvg .useIcon').attr('fill', 'black');
                $('.amclReflectionSvg .useIcon2').attr('fill', 'black');
            }else{
                $('.amclReflectionSvg .useIcon').attr('fill', 'LightGrey');
                $('.amclReflectionSvg .useIcon2').attr('fill', 'LightGrey');
            }
        }
    },

    mounted(){
        //判断localStorage中 IP 是否有值，若无设置默认值
        if(!localStorage.getItem('ip')){
            localStorage.setItem('ip', 'localhost:8181');
        }
    },

    methods:{
        nodeExpand(data) {
          this.expandedList.push(data.label); // 在节点展开是添加到默认展开数组
	    },
	    nodeCollapse(data) {
	      this.expandedList.splice(this.expandedList.indexOf(data.code), 1); // 收起时删除数组里对应选项
	    },
        //flexbe 取消按钮
        flexBePauseBtn: function(){
            if(pubFlexbePause()){
                this.iconData = 'el-icon-video-play'
                this.isClickPause = true
            }else{
                this.iconData = 'el-icon-video-pause'
                this.isClickPause = false
            }
        },

        //flexbe 暂停按钮
        flexBePreemptBtn: function(){
            pubFlexbePreempt()
        },

        //定位
        // clickLocationBtn: function(){
        //     const that = this;
        //     locationRobot();
        // },

        // 选择地图
        chooseMap(topicName, mapName){
            displayMap(topicName, mapName)
    //         if(mapName == 'map'){
    //             this.isClickMap = ture
    //             this.isClickMapPath = false
    //             this.isClickMapDisplay = false
    //             this.isClickMapReflection = false
    //         }else if(mapName == 'map_path'){
    //             this.isClickMap = false
    //             this.isClickMapPath = ture
    //             this.isClickMapDisplay = false
    //             this.isClickMapReflection = false
    //         }else if(mapName == 'map_display'){
    //             this.isClickMap = false
    //             this.isClickMapPath = false
    //             this.isClickMapDisplay = ture
    //             this.isClickMapReflection = false
    //         }else{
    //             this.isClickMap = false
    //             this.isClickMapPath = false
    //             this.isClickMapDisplay = false
    //             this.isClickMapReflection = ture
    //         } 
        },

        //下载地图
        downloadMap: function(){
            downMapPng();
        },

        //建图
        mappingBtn: function(){
            this.$emit("transferSetting", "mapping");
            showNewMap(true);
        },

        //导出地图
        uploadMap: function(){
            showUploadMap(true);
        },
        
        //放大缩小
        mapZoom:function(type){
            let that = this;
            if(type == 'max'){
                that.$store.commit("zoomDisabled", 0)
                setTimeout(function(){
                    zoomIn('zoomIn');
                }, 300)
            }else{
                that.$store.commit("zoomDisabled", 1)
                setTimeout(function(){
                    zoomIn('zoomOut');
                }, 300)
            }
        },
        //手动定位传值
        settingVal(val){
            this.tabModel = "map";
            switch(val){
                case "joystick":
                    displayJoystick(true);
                    break;
                case "initialPose":
                    initPoseFn('initPose');
                    break;
                case "goal":
                    initPoseFn('goal');
                    break;
                case "mapping":
                    break;
                default:
                    break;
            }
        },
        //flexbe 任务下发后手动定位到tab==map
        flexbeVueVal(val){
            this.tabModel = "map";
        },
        waypointVal(val){
            this.tabModel = "map";
        },

        //树形结构状态显示自定义颜色函数
        renderContent(h, { node, data, store }) {
            var statusColor
            switch (data.levelnum){
                case 0:
                    statusColor = <span class="custom-tree-node tree-node-ok" ><span>{ node.label }</span><span>{ data.levelnum }</span></span>
                    break;
                case 1:
                    statusColor = <span class="custom-tree-node tree-node-warring" ><span>{ node.label }</span><span>{ data.levelnum }</span></span>
                    break;
                case 2:
                    statusColor = <span class="custom-tree-node tree-node-error" ><span>{ node.label }</span><span>{ data.levelnum }</span></span>
                    break;
                default:
                    statusColor = <span class="custom-tree-node tree-node-null" ><span>{ node.label }</span><span>{ data.levelnum }</span></span>
                    break;
            }
            return statusColor
      }
    }
}
</script>

<style scoped lang="less">
    .clickMap{
        border: #3b6dde 3px solid;
        box-sizing: border-box;
    }
    .amclOrigin{
        position: absolute;
        // top: 15px;
        left: -10px;
        height: 46px;
        width: 100px;
        font-size: 12px;
        z-index: 10000;
    }
    .amclReflection{
        position: absolute;
        left: 70px;
        height: 46px;
        width: 100px;
        font-size: 12px;
        z-index: 10000;
    }
    .page{
        position: relative;
        padding: 15px 0px;
        font-size: 14px;
        height: 100%;
        overflow: hidden;
    }
  .moreBox{
    width: 100%;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
    .moreItem{
        font-size: 12px;
        margin: 10px 0px;
        width: 33.333%;
        text-align: center;
        flex-direction: column;
        .icon{
            width: 50px;
            height: 50px;
            border-radius: 4px;
            background-position: center;
            background-repeat: no-repeat;
            background-size: 40%;
            margin-bottom: 5px;
        }
        .mapNor{
            background-image: url("../../src/assets/icon-map-nor.png");
        }
    }
    .itemTitle{
        width: 61px;
        overflow: hidden;
        white-space: nowrap;
    }
    .boxShadow{
        font-size: 24px;
        color: #616161;
    }
  }
  .mapBox{
    width: 100%;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
    .moreItem{
        font-size: 12px;
        margin: 10px 0;
        width: 25%;
        text-align: center;
        flex-direction: column;
        .icon{
            width: 50px;
            height: 50px;
            border-radius: 4px;
            background-position: center;
            background-repeat: no-repeat;
            background-size: auto;
            margin-bottom: 5px;
        }
        .map{
            background-image: url("../../src/assets/map.png");
        }
        .mapPath{
            background-image: url("../../src/assets/map_path.png");
        }
        .mapDisplay{
            background-image: url("../../src/assets/map_display.png");
        }
        .mapReflection{
            background-image: url("../../src/assets/map_reflection.png");
        }
    }
  }
    .flexBePreemptBtn{
        width: 40px;
        height: 40px;
        border-radius: 4px;
        position: absolute;
        bottom: 65px;
        left: 15px;
        z-index: 994;
        button{
            padding: 0;
            border: none;
            font-size: 26px;
            color: #9b9c9e;
            width: 100%;
            height: 100%;
        }
    }
    .flexBePauseBtn{
        width: 40px;
        height: 40px;
        border-radius: 4px;
        position: absolute;
        bottom: 110px;
        left: 15px;
        z-index: 994;
        button{
            padding: 0;
            border: none;
            font-size: 26px;
            color: #9b9c9e;
            width: 100%;
            height: 100%;
        }
    }
    .clickPause{
        button{
            padding: 0;
            border: none;
            font-size: 26px;
            color: #9b9c9e;
            width: 100%;
            height: 100%;
        }
    }
    .mapLayer{
        width: 40px;
        height: 40px;
        border-radius: 4px;
        position: absolute;
        top: 20px;
        right: 15px;
        z-index: 994;
        button{
            padding: 0;
            border: none;
            font-size: 26px;
            color: #606266;
            width: 100%;
            height: 100%;
        }

    }
    
    .mapSizeBtn{
        flex-direction: column;
        width: 40px;
        height: 80px;
        border-radius: 4px;
        position: absolute;
        bottom: 65px;
        right: 15px;
        z-index: 994;
        background-color: #ffffff;
        .zoomBtn{
            width: 100%;
            height: 40px;
            padding: 0;
            margin-left: 0;
            border: none;
        }
    }

    .tabs{
        .tabItem{
            flex-direction: column;
            font-size: 25px;
            height: 60px;
            span{
                font-size: 14px;
                line-height: 1;
                margin-top: 5px;
            }
        }
        .tabItem:hover{
            background: #eeeeee;
            border-radius: 3px;
        }
    }
    .dividingLine{
        width: 100%;
        height: 25px;
        background: #eeeeee;
        line-height: 25px;
        p{
            text-align: left;
            padding-left: 15px;
            color: #aaaaaa;
        }
    }
    
</style>


