<template>
    <div class="waypoints">
        <div class="searchBox">
            <el-input placeholder="搜索站点名称" v-model="inputWaypoint" class="input-with-select" @input="InputChange">
            </el-input>
        </div>

        <el-collapse accordion v-model="activeWaypoint">
            <el-collapse-item v-for="(item,index) in waypoints" :key="index" :name="item.name">
                <template slot="title">
                <i class="header-icon el-icon-location-information waypointName"></i>
                <span class="nameBox">{{item.name}}</span>
                <span class="desc" v-if="item.name === 'lift_up_wp'">AGV顶升上升</span>
                <span class="desc" v-if="item.name === 'lift_down_wp'">AGV顶升下降</span>
                <span class="desc" v-if="item.name === 'home_point'">开机默认初始化站点</span>
                <span class="desc" v-if="item.name === 'charge_point'">开机默认充电站点</span>
                </template>
                <div>
                    <waypointDetail :details="item" @transferSetting = "waypointDetailVal"></waypointDetail>
                </div>
            </el-collapse-item>
            <el-divider></el-divider>
        </el-collapse>
        <div class="flex searchAddGroup">
            <el-button type="primary" icon="el-icon-plus" circle @click.stop="addBtn"></el-button>
        </div>
    </div>    
</template>

<script>
import waypointDetail from "@/components/pages/modules/waypointDetail.vue"
import { showWaypointList, showWaypoint } from '../../js/waypoint'
import { debounce } from '../../js/methods'

export default {
    name: 'waypoints',
    components:{
       waypointDetail
    },
    data(){
        return{
            waypoints: [],
            inputWaypoint: "",
            waypointsList: [],
            activeWaypoint: ""
        }
    },

    //计算属性
    watch:{
        //站点列表,数据过滤，frae_id转换json，判断display
//         '$store.state.stationList':function(val){
//            val.forEach(element => {
//             if (element.name.indexOf('joy_') < 0 && element.name.indexOf('wheel_') < 0 && element.name.indexOf('param_') < 0 
// && element.name.indexOf('_pos') < 0 && element.name.indexOf('_vel') < 0 ) {
//                 this.waypoints.push(element)
//                 this.waypointsList.push(element)
//             }
//            });
//        },
        //站点列表,数据过滤，frae_id转换json，判断display
       '$store.state.stationList':function(val){
           val.forEach(element => {
            let frameId = JSON.parse(element.frame_id);
               if(frameId.display != 'true'){
                   this.waypoints.push(element);
                   this.waypointsList.push(element);
               }
           });
       },

       //点击站点图标，展示对应站点
       "$store.state.wayPointMsg": function(val) {
           this.activeWaypoint = val.name
       }
    },

    mounted(){
        
    },

    methods:{
        //搜索站点
        InputChange: debounce(function(){
            let that = this;
            if(that.inputWaypoint){
                that.waypoints = [];
                for(var i in that.waypointsList){
                    if(that.waypointsList[i].name.indexOf(that.inputWaypoint) >= 0){
                        that.waypoints.push(that.waypointsList[i])
                    }
                }
            }else{
                that.waypoints = that.waypointsList;
            }
        }, 300),
       
        //添加站点
        addBtn(){
            showWaypointList(false);
            showWaypoint(true);
        },

        waypointDetailVal(val){
            this.$emit("transferSetting", "waypoints")
        }
    }
}
</script>

<style scoped lang="less">
    .waypoints{
        position: relative;
        font-size: 14px;
        height: 100%;
        font-size: 16px;
        background-color: #eeeeee;
        .searchBox{
            padding: 15px;
            background-color: #ffffff;
        }
        .waypointName{
            color: #409EFF;
        }
        .desc{
            font-size: 12px;
            color: #909399;
            margin-left: 10px;
        }
    }
    .searchAddGroup{
        position: fixed;
        flex-direction: column;
        right: 10px;
        bottom: 70px;
        button{
            margin-bottom: 20px;
        }
    }
</style>


