<template>
        <div class="trajectoryBox">
            <el-form ref="ruleForm" :model="trajectoryForm" class="trajForm">
                <div class="flex trajGroup">
                    <div class="trajTypeGroup">
                        <el-form-item>
                            <div class="flex formItem trajNameItem">
                                <div class="itemLeft">轨迹名称:</div>
                                <div class="itemRight">
                                    <input type="text" placeholder="请输入轨迹名称" v-model="trajectoryForm.name" :disabled="!edit">
                                </div>
                            </div>
                        </el-form-item>

                        <el-form-item>
                            <div class="flex formItem">
                                <div class="itemLeft">选择站点:</div>
                                <div class="itemRight">
                                    <el-select
                                    v-model="trajectoryForm.waypoints"
                                    placeholder="请选择站点"
                                    multiple
                                    size="mini"
                                    class="waypointSelect"
                                    popper-class="selectClass"
                                    @keyup.enter="subUpdate"
                                    :disabled="!edit"
                                    >
                                    <el-option
                                        v-for="(item,index) in this.$store.state.stationList"
                                        :key="index"
                                        :label="item.name"
                                        :value="item.name"
                                    ></el-option>
                                    </el-select>
                                </div>
                            </div>
                        </el-form-item>
                    </div>
                    <div class="flex trajBtnGroup">
                        <div class="goHere" @click.self="goHere(trajectoryForm.name)"></div>
                        <el-button :type="edit? 'success':'warning'" :icon="edit? 'el-icon-check':'el-icon-edit'" circle class="editBox" @click="editTrajectory(trajectoryForm.name)" v-show="develop"></el-button>
                        <el-button type="danger" icon="el-icon-delete" circle @click="delTrackBtn(trajectoryForm.name)" v-show="develop"></el-button>
                    </div>
                </div>
            </el-form>
        </div>
</template>

<script>
import { showTrajectoryDetail, submitAddTrack, navTrack, delTrack } from '@/js/trajectory'
export default {
    name: 'trajectoryDetail',
    components:{

    },
    props:["details"],
    data(){
        return{
            develop: this.$store.state.userRole === "developer" ? true : false,
            showMore: true,
            edit: false,
            trajectoryForm: {
                name: "",
                waypoints: []
            },
            delTrajName: null
        }
    },
    methods:{

        //轨迹信息
        updateTrajectoryMsg(info){
            this.trajectoryForm.name = info.name;
            let waypointsArr = [];
            if(info.waypoints.length !== 0){
                info.waypoints.forEach(element => {
                    waypointsArr.push(element.name);
                });
                this.trajectoryForm.waypoints = [...new Set(waypointsArr)];
            }
        },

        //编辑轨迹,提交
        editTrajectory(name){
            let loadingFlag = null;
            if(this.edit){
                loadingFlag = this.$loading({
                    text: "修改中..."
                });
                if(this.trajectoryForm.type === "goal"){
                    this.trajectoryForm.type = 'goal'
                }
                if(this.trajectoryForm.mode === 'LOOP'){
                    this.trajectoryForm.mode = 'LOOP'
                }
                if(this.trajectoryForm.voiceMode === 'play'){
                    this.trajectoryForm.voiceMode = 'LOOP'
                }
                console.log(this.trajectoryForm)
                submitAddTrack(this.trajectoryForm, this.delTrajName, true);
                loadingFlag.close();
            }else {
                this.delTrajName = name;
            }
            this.edit = !this.edit;
        },

        //点击站点去这里
        goHere(name){
            this.$notify({
                title: "成功",
                message: `现在开始导航至轨迹 ${name}`,
                type: 'success',
                position: 'top-left',
                offset: 100
            });
            navTrack(name);
        },

        //点击删除轨迹
        delTrackBtn:function(name){
            const that = this;
            that.$confirm(`是否永久删除${name}轨迹?`, '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
                center: true,
                customClass: "delBranchCustom"
            }).then(() => {
                delTrack(name);
            }).catch(() => {
                          
            });
        },
    },

    created(){
        try{
            this.updateTrajectoryMsg(this.details)
        }
        catch(err){
            console.log(err)
        }
    },

    //属性监听
    watch:{
      '$store.state.userRole':function(val){
          if(val === "developer"){
              this.develop = true;
          }else {
              this.develop = false;
          }
        },
        details: function(val){
            this.updateTrajectoryMsg(val)
        }
    }
}
</script>

<style scoped lang="less">
.trajectoryBox{
    padding: 0 15px;
    position: relative;
    .trajForm {
        .trajGroup{
            justify-content: space-between;
            .trajTypeGroup{
                width: calc(100% - 60px);
                .trajNameItem{
                    border-bottom: 1px solid #eeeeee;
                }
            }
            .trajBtnGroup{
                flex-direction: column;
                width: 60px;
                height: 164px;
                align-items: flex-end;
                button{
                    margin-left: 0;
                }
                .editBox{
                    margin: 10px 0;
                }
                .goHere{
                    width: 46px;
                    height: 46px;
                    background: url("../../../../src/assets/go.png") no-repeat center;
                    background-size: cover;
                    display: inline-block;
                }
            }
        }
        .itemLeft {
            flex: 4;
        }
        .itemRight {
            height: 100%;
            flex: 6;
            input {
                width: 100%;
                height: 40px;
                line-height: 40px;
                display: block;
                font-size: 14px;
            }
        }
    }
  .waypointSelect {
      display: flex;
      justify-content: center;
      min-height: 40px;
    input {
    height: 10px;
    }
    .el-input__icon {
      height: inherit;
    }
    .el-input__inner {
      height: normal;
      line-height: normal;
    }
    width: 100%;
    border: none;
    outline: none;
    padding-left: 0.7em;
  }
  .formBtns {
    padding: 0 5px;
    margin-top: 15px;
    button {
      width: 100%;
    }
  }
  .el-form-item {
    margin-bottom: 0;
  }
  .el-form-item__content {
    line-height: 20px;
  }
}
</style>


