import store from '../store/index';
import { OBJ } from './init'
/**
 * 显示站点列表
 * flag :true显示  :false隐藏
 * **/
export function showVolume(flag){
    if(flag){
        store.commit('switchvolume', true);
    }else{
        store.commit('switchvolume', false);
    }
}

/**
 * 调节声音
 * **/
export function adjustVoice(num) {
    let message = {
        seq: num,
        frame_id: 'system_volume'
    }
    var voiceMessage = new ROSLIB.Message(message);
    OBJ.topic['/task_switch'].publish(voiceMessage);
}



