<template>
    <div class="detailBox">
        <!-- 站点详情 -->
        <el-form ref="ruleForm" class="waypointForm">
            <div class="flex waypointTopGroup">
                <div class="waypointTypeGroup">
                    <el-form-item>
                        <div class="flex formItem">
                            <div class="itemLeft">站点名称:</div>
                            <div class="itemRight"><input type="text" placeholder="请输入站点名称" v-model="waypointForm.stationName" :disabled="!edit"></div>
                        </div>
                    </el-form-item>

                    <el-form-item>
                        <div class="flex formItem">
                            <div class="itemLeft">站点类型:</div>
                            <div class="itemRight">
                                <el-select v-model="waypointForm.type" placeholder="请选择站点类型" @change="changeStationType" size="mini" class="stationType" popper-class="selectClass" :disabled="!edit">
                                    <el-option
                                    v-for="item in waypointForm.stationType"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                                    </el-option>
                                </el-select>
                            </div>
                        </div>
                    </el-form-item>

                    <!-- 默认站点模式 -->
                    <el-form-item class="" v-show="showStationMode">
                        <div class="flex formItem">
                            <div class="itemLeft">站点模式:</div>
                            <div class="itemRight">
                                <el-select v-model="waypointForm.mode" placeholder="请选择站点模式" size="mini" class="stationType" popper-class="selectClass" :disabled="!edit">
                                    <el-option
                                    v-for="item in waypointForm.stationMode"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                                    </el-option>
                                </el-select>
                            </div>
                        </div>
                    </el-form-item>

                    <!-- 站点列表模式 -->
                    <el-form-item class="" v-show="showGotoStationMode">
                        <div class="flex formItem">
                            <div class="itemLeft">站点模式:</div>
                            <div class="itemRight">
                                <el-select v-model="waypointForm.gotoMode" placeholder="请选择站点模式" size="mini" class="stationType" :disabled="!edit">
                                    <el-option
                                    v-for="item in this.$store.state.stationList"
                                    :key="item.name"
                                    :label="item.name"
                                    :value="item.name">
                                    </el-option>
                                </el-select>
                            </div>
                        </div>
                    </el-form-item>

                    <!-- call列表模式 -->
                    <el-form-item class="" v-show="showCallStationMode">
                        <div class="flex formItem">
                            <div class="itemLeft">站点模式:</div>
                            <div class="itemRight">
                                <el-select v-model="waypointForm.callMode" placeholder="请选择站点模式" size="mini" class="stationType" :disabled="!edit">
                                    <el-option
                                    v-for="item in this.$store.state.trajectoryList"
                                    :key="item.name"
                                    :label="item.name"
                                    :value="item.name">
                                    </el-option>
                                </el-select>
                            </div>
                        </div>
                    </el-form-item>

                    <!-- 声音站点模式 -->
                    <el-form-item class="" v-show="showVoiceStationMode">
                        <div class="flex formItem">
                            <div class="itemLeft">站点模式:</div>
                            <div class="itemRight">
                                <el-select v-model="waypointForm.voiceMode" placeholder="请选择站点模式" size="mini" class="stationType" :disabled="!edit">
                                    <el-option
                                    v-for="item in waypointForm.voiceStationMode"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                                    </el-option>
                                </el-select>
                            </div>
                        </div>
                    </el-form-item>

                    <el-form-item class="" v-show="showFrameId">
                        <div class="flex formItem">
                            <div class="itemLeft">frame_id:</div>
                            <div class="itemRight"><input type="text" placeholder="请输入frame_id" v-model="waypointForm.frameId" :disabled="!edit"></div>
                        </div>
                    </el-form-item>

                    <el-form-item class="" v-show="showSubscribeValue">
                        <div class="flex formItem">
                            <div class="itemLeft">发布-订阅器接收值:</div>
                            <div class="itemRight"><input type="text" placeholder="请输入接收值" v-model="waypointForm.subscribeValue" :disabled="!edit"></div>
                        </div>
                    </el-form-item> 
                </div>
                <div class="flex waypointBtnGroup">
                    <div class="goHere" @click.self="goHere()"></div>
                    <el-button :type="edit? 'success':'warning'" :icon="edit? 'el-icon-check':'el-icon-edit'" circle class="editBox" @click="editWaypoint(waypointForm.stationName)" v-show="develop"></el-button>
                    <el-button type="danger" icon="el-icon-delete" circle @click="deleteWaypointBtn(waypointForm.stationName)" v-show="develop"></el-button>
                </div>
            </div>

            <!-- <el-form-item class="">
                <div class="flex formItem">
                    <div class="itemLeft">范围</div>
                    <div class="itemRight"><input type="text" placeholder="请输入站点范围" v-model="waypointForm.scope" :disabled="!edit"></div>
                </div>
            </el-form-item> -->

            <!-- <el-form-item class="">
                <div class="flex formItem">
                    <div class="itemLeft">超时时间</div>
                    <div class="itemRight"><input type="text" placeholder="请输入超时时间" v-model="waypointForm.timeout" :disabled="!edit"></div>
                </div>
            </el-form-item> -->

            <el-form-item class="" v-show="showPosition">
                <div class="localTitle">Positon:</div>
                <div class="flex formItem bottomNone">
                    <div class="localInputItem">
                        <div class="itemLeft">x</div>
                        <div class="itemRight">
                            <input type="text" placeholder="" v-model="waypointForm.positionX" :disabled="!edit">
                        </div>
                    </div>

                    <div class="localInputItem">
                        <div class="itemLeft">y</div>
                        <div class="itemRight">
                            <input type="text" placeholder="" v-model="waypointForm.positionY" :disabled="!edit">
                        </div>
                    </div>

                    <div class="localInputItem">
                        <div class="itemLeft">z</div>
                        <div class="itemRight">
                            <input type="text" placeholder="" v-model="waypointForm.positionZ" :disabled="!edit">
                        </div>
                    </div>
                </div>
            </el-form-item>

            <el-form-item class="" v-show="showOrientation">
                <div class="localTitle">Orientation:</div>
                <div class="flex formItem bottomNone">
                    <div class="localInputItem">
                        <div class="itemLeft">x:</div>
                        <div class="itemRight">
                            <input type="text" placeholder="" v-model="waypointForm.orientationX"  @change="changeOrientation" :disabled="!edit">
                        </div>
                    </div>
                    <div class="localInputItem">
                        <div class="itemLeft">y:</div>
                        <div class="itemRight">
                            <input type="text" placeholder="" v-model="waypointForm.orientationY"  @change="changeOrientation" :disabled="!edit">
                        </div>
                    </div>
                    <div class="localInputItem">
                        <div class="itemLeft">z:</div>
                        <div class="itemRight">
                            <input type="text" placeholder="" v-model="waypointForm.orientationZ"  @change="changeOrientation" :disabled="!edit">
                        </div>
                    </div>
                    <div class="localInputItem">
                        <div class="itemLeft">w:</div>
                        <div class="itemRight">
                            <input type="text" placeholder="" v-model="waypointForm.orientationW"  @change="changeOrientation" :disabled="!edit">
                        </div>
                    </div>
                </div>
            </el-form-item>

            <el-form-item class="" v-show="showYaw">
                <div class="flex formItem">
                    <div class="itemLeft">Yaw(deg):</div>
                    <div class="itemRight"><input type="text" placeholder="请输入Yaw" v-model="waypointForm.yaw" @change="changeYaw" @keyup.enter="subUpdate" :disabled="!edit"></div>
                </div>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import { quaternionToYaw } from '@/js/methods'
import { showWaypointDetail, deleteWaypoint, submitAddWaypoint } from '@/js/waypoint'
import { navNewStation } from '@/js/navigation'

export default {
    name: 'waypointDetail',
    components:{

    },
    props:["details"],
    data(){
        return{
            edit: false,
            develop: this.$store.state.userRole === "developer" ? true : false,
            waypointForm:{
                stationName:'',
                stationType: [{
                    value: 'goal',
                    label: "目标站点"
                }, {
                    value: 'dock',
                    label: "充电桩"
                }, {
                    value: 'timer',
                    label: "计时器"
                }, {
                    value: 'publisher',
                    label: "发布器"
                }, {
                    value: 'subscriber',
                    label: "订阅器"
                }, {
                    value: 'looper',
                    label: "循环"
                }, {
                    value: 'pubsuber',
                    label: "发布-订阅器"
                }, {
                    value: 'cmd_vel_set_sub',
                    label: "最大速度设置"
                }, {
                    value: 'cmd_vel',
                    label: "速度发布器"
                }, {
                    value: 'shell',
                    label: "命令发布器"
                }, {
                    value: 'sound_play',
                    label: "声音播放"
                }, {
                    value: 'initial_pose',
                    label: "初始位置"
                }, {
                    value: 'scan_marker',
                    label: "充电桩定位"
                }, {
                    value: 'shell_string',
                    label: "Shell命令"
                }, {
                    value: 'pallet',
                    label: 'Pallet'
                }, {
                    value: 'bias',
                    label: 'Bias'
                }, {
                    value: 'call',
                    label: 'Call'
                }, {
                    value: 'goto',
                    label: 'Goto'
                }],
                type: "goal",
                stationMode:[{
                    value: 'LOOP',
                    label: 'LOOP',
                }, {
                    value: 'LOOP2',
                    label: 'LOOP2'
                }, {
                    value: 'BACK',
                    label: 'BACK'
                }, {
                    value: 'BACK2',
                    label: 'BACK2'
                }, {
                    value: 'NONE',
                    label: "单次"
                }],
                mode: 'LOOP',
                gotoMode: '',
                callMode: '',
                voiceMode: 'play',
                voiceStationMode:[{
                    value: 'LOOP',
                    label: "播放",
                }, {
                    value: 'LOOP2',
                    label: "停止",
                }, {
                    value: 'BACK',
                    label: "播放一次",
                },],
                frameId:'map',
                subscribeValue: 0,
                positionX: 0.0,
                positionY: 0.0,
                positionZ: 0.0,
                orientationX: 0.0,
                orientationY: 0.0,
                orientationZ: 0.0,
                orientationW: 0.0,
                yaw: 0.0,
                // scope: 0.0,
                // timeout: 0.0
            },
            showFrameId: true,
            showSubscribeValue: false,
            showStationMode: true,
            showVoiceStationMode: false,
            showGotoStationMode: false,
            showCallStationMode: false,
            showPosition: true,
            showOrientation: true,
            showYaw:true,
            deleteWaypointName: null
        }
    },
    methods:{
        //点击站点去这里
        goHere(){
            this.$notify({
                title: "成功",
                message: "现在开始导航至站点 " + this.waypointForm.stationName,
                type: 'success',
                position: 'top-left',
                offset: 100
            });
            navNewStation(this.waypointForm.stationName);
            showWaypointDetail(this.waypointForm.stationName);
            this.$emit("transferSetting", "waypoints")
        },

        //编辑站点,提交
        async editWaypoint(name){
            const that = this;
            let loadingFlag = null;
            if(this.edit){
                loadingFlag = that.$loading({
                    text: "修改中..."
                });
                if(this.waypointForm.type === "goal"){
                    this.waypointForm.type = 'goal'
                }
                if(this.waypointForm.mode === 'LOOP'){
                    this.waypointForm.mode = 'LOOP'
                }
                if(this.waypointForm.voiceMode === 'play'){
                    this.waypointForm.voiceMode = 'LOOP'
                }
                submitAddWaypoint(that.waypointForm, that.deleteWaypointName, true);
                loadingFlag.close();
            }else {
                that.deleteWaypointName = name;
            }
            this.edit = !this.edit;
        },

        //删除站点
        deleteWaypointBtn(name){
            const that = this;
            that.drawer = false;
            that.$confirm(`是否永久删除${name}站点?`, '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
                center: true,
                customClass: "delBranchCustom"
            }).then(() => {
                deleteWaypoint(name);
            }).catch(() => {
                          
            });
        },

        //选择站点类型
        changeStationType(type){
            let that = this;
            that.showFrameId = false;
            that.showSubscribeValue = false;
            that.showStationMode = true,
            that.showVoiceStationMode = false;
            that.showGotoStationMode = false;
            that.showCallStationMode = false;
            that.showPosition = true;
            that.showOrientation = true;
            that.showYaw = true;
            switch(type){
                case 'goal':
                that.showFrameId = true;
                break;

                case 'pubsuber':
                that.showSubscribeValue = true;
                break;

                case 'sound_play':
                that.showStationMode = false;
                that.showVoiceStationMode = true;
                that.showGotoStationMode = false;
                that.showCallStationMode = false;
                break;

                case 'goto':
                that.showStationMode = false;
                that.showVoiceStationMode = false;
                that.showGotoStationMode = true;
                that.showCallStationMode = false;
                break;

                case 'call':
                that.showStationMode = false;
                that.showVoiceStationMode = false;
                that.showGotoStationMode = false;
                that.showCallStationMode = true;
                break;

                case 'initial_pose':
                that.showFrameId = true;
                break;

                default:
                
                break;
            }
        },

        //改变orientation
        changeOrientation(){
            let that = this;
            let orientation = {
                x: parseFloat(this.waypointForm.orientationX),
                y: parseFloat(this.waypointForm.orientationY),
                z: parseFloat(this.waypointForm.orientationZ),
                w: parseFloat(this.waypointForm.orientationW),
            }
            //有效的四元数转换
            if (isQuaternionValid(orientation)){
                let yawRad = quaternionToYaw(orientation);
                let yawDeg = yawRad * 180 / Math.PI;
                that.waypointForm.yaw = yawDeg.toFixed(5);
            }else{
                this.$notify({
                    title: "警告",
                    message: "四元数不合法",
                    type: 'warning',
                    position: 'top-left',
                    offset: 100
                });
                return
            }
        },

        /**
         * 改变yaw,转换为四元数
         * **/
        changeYaw(){
            let that = this;
            let yawDeg = parseFloat(that.waypointForm.yaw);
            let orientation = thetaToQuaternion(yawDeg/180*Math.PI);
            that.waypointForm.orientationX = orientation.x.toFixed(5);
            that.waypointForm.orientationY = orientation.y.toFixed(5);
            that.waypointForm.orientationZ = orientation.z.toFixed(5);
            that.waypointForm.orientationW = orientation.w.toFixed(5);
        },

        //初始化站点参数
        updateWaypointMsg(waypointInfo){
            //站点类型转换
            let details = JSON.parse(waypointInfo.frame_id);
            this.waypointForm.stationName = waypointInfo.name;
            this.waypointForm.type = details.type;
            this.waypointForm.mode = details.failure_mode;
            // if(details.close_enough){
            //     this.waypointForm.scope = Number(details.close_enough).toFixed(3);
            // }else{
            //     this.waypointForm.scope = Number(waypointInfo.close_enough).toFixed(3);
            // }

            // if(details.goal_timeout){
            //     this.waypointForm.timeout = Number(details.goal_timeout).toFixed(3);
            // }else{
            //     this.waypointForm.timeout = Number(waypointInfo.goal_timeout).toFixed(3)
            // }
            this.waypointForm.positionX = Number(waypointInfo.pose.position.x).toFixed(3);
            this.waypointForm.positionY = Number(waypointInfo.pose.position.y).toFixed(3);
            this.waypointForm.positionZ = Number(waypointInfo.pose.position.z).toFixed(3);
            this.waypointForm.orientationX = Number(waypointInfo.pose.orientation.x).toFixed(3);
            this.waypointForm.orientationY = Number(waypointInfo.pose.orientation.y).toFixed(3);
            this.waypointForm.orientationZ = Number(waypointInfo.pose.orientation.z).toFixed(3);
            this.waypointForm.orientationW = Number(waypointInfo.pose.orientation.w).toFixed(3);
            var yaw = quaternionToYaw(waypointInfo.pose.orientation);
            var yawDeg = yaw / Math.PI * 180;
            this.waypointForm.orientionRAD = Number(yaw).toFixed(3);
            this.waypointForm.orientionDEG = Number(yawDeg).toFixed(3);
        }     
    },

    created(){
        try{
            this.updateWaypointMsg(this.details)
        }
        catch(err){
            console.log(err)
        }
    },

    mounted(){
        
    },

    //属性监听
    watch:{
       '$store.state.userRole':function(val){
           if(val === "developer"){
               this.develop = true;
           } else{
               this.develop = false;
           }
        },
        details: function(val){
            this.updateWaypointMsg(val)
        }
    }

}
</script>

<style scoped lang="less">
.detailBox{
    position: relative;
    
}
.waypointForm{
    padding: 0 15px;
    .waypointTopGroup{
        justify-content: space-between;
        .waypointTypeGroup{
            width: calc(100% - 60px);
        }
        .waypointBtnGroup{
            flex-direction: column;
            width: 60px;
            height: 164px;
            align-items: flex-end;
            button{
                margin-left: 0;
            }
            .editBox{
                margin: 10px 0;
            }
            .goHere{
                width: 46px;
                height: 46px;
                background: url("../../../../src/assets/go.png") no-repeat center;
                background-size: cover;
                display: inline-block;
            }
        }
    }
    .formItem{
        border-bottom: 1px solid #eeeeee;
        .itemLeft{
            flex: 4;
            text-align: left;
            font-size: 14px;
        }
        .itemRight{
            flex: 6;
            height: 40px;
            input{
                width: 100%;
                height: 100%;
                display: block; 
                font-size: 14px;
                height: 40px;
                line-height: 40px;         
            }
            .selectClass{
                display: flex;
                justify-content: center;
                align-items: center
            }
        }
    }
    .bottomNone{
        border-bottom: none;
    }

     .localTitle{
        text-align: left;
    }
    .localInputItem{
        text-align: center;
        padding: 0 5px;
        .itemLeft{
            text-align: center;
            height: 20px;
            line-height: 1;
        }
        input{
            border: 1px dashed #cecece;
            padding: 5px 0;
            text-align: center;
            margin-top: 2px;
        }
    }
    .stationType{
        input{
            height: 10px;
        }
        .el-input__icon{
            height:inherit
        }
        .el-input__inner{
            height: normal;
            line-height: normal
        }
        width: 100%;
        border: none;
        outline: none;
        padding-left: 0.7em;
        height: 100%;
    }
     .formBtns{
        padding: 0 5px;
        margin-top: 15px;
        button{
            width: 100%;
        }
    }
    .el-form-item{
        margin-bottom: 0;
    }
    .el-form-item__content{
        line-height: 20px;
    }
}
</style>


