import vue from '../main'
import store from '../store/index';
import { Loading, Notification  } from 'element-ui';
import { OBJ, viewer, zoomview } from './init'
import { PARAMS } from "./params"
import { showWayPoints2 } from "./waypoint"
import { robotMarker } from './robot'
/***
 * 建图函数
 */
export function displayMap(topicName = '/map_ostream_display', mapName = 'map_display'){
    store.commit('nowShowMapName', mapName);
    return new Promise(function(resolve, reject){
        let tname = topicName.replace('/', '');
        if(store.state[tname + 'Msg']){
            mapShow(store.state[tname + 'Msg'])
        }
        // OBJ.topic['map5Topic'].subscribe(function(msg){
        OBJ.topic[topicName].subscribe(function(msg){
            store.commit(tname + "Msg", msg);
            console.log(msg)
            mapShow(msg)            
            resolve("display map")
        })
    })
}
function mapShow(msg){
    var imgMap = new ROS2D.ImageMap({
        image: msg.data,
        message: msg.info
    });
    
    //使用防抖更新地图base64数据
    store.commit("base64MapData", msg.data);
    OBJ.mapMsg = msg;

    if(OBJ.imgMapStage){
        viewer.scene.removeAllChildren();
    }
    OBJ.imgMapStage = imgMap;
    viewer.scene.addChild(imgMap);
    let rmap = OBJ.mapInfo.windowWidth / imgMap.width;
    viewer.width = OBJ.mapInfo.windowWidth;
    viewer.height = imgMap.height * rmap;
    
    if(store.state.rosMode !== "gmapping"){
        viewer.scaleToDimensions(imgMap.width, imgMap.height);
        viewer.shift(imgMap.pose.position.x, imgMap.pose.position.y);
    }else{
        if(store.state.mappingFlag === false){
            viewer.scaleToDimensions(imgMap.width, imgMap.height);
            viewer.scene.x = OBJ.mapInfo.windowWidth / 2;
            viewer.scene.y = OBJ.mapInfo.windowHeight / 2;
            store.commit("mappingFlag", true);
        }
    }
    var mapPosition = {
        x: viewer.scene.x,
        y: viewer.scene.y
    }
    OBJ.mapXY = mapPosition;
    robotMarker();
}

/**
 * 控制放大
 *  一秒切换logo过度效果
 * **/
export function zoomIn(scale) {
    let scaleNum = Number(store.state.scale);
    store.commit("zoomDisabled", 2)
    if (scale === 'zoomIn') {
        store.commit('setScale', scaleNum *= 1.2);
        if (scaleNum > 2.5) {
            store.commit('zoomDisabled', 0);
            return
        }
        showWayPoints2();
        robotMarker();
        zoomview.startZoom(viewer.scene.x + OBJ.robotStage.x, viewer.scene.y + OBJ.robotStage.y);
        zoomview.zoom(1.2);
    } else {
        store.commit('setScale', scaleNum *= 0.8);
        if (scaleNum < 0.32) {
            store.commit('zoomDisabled', 1);
            return
        }
        showWayPoints2();
        robotMarker();
        zoomview.startZoom(viewer.scene.x + OBJ.robotStage.x, viewer.scene.y + OBJ.robotStage.y);
        zoomview.zoom(0.8);
    }
};

/**
 * 显示建图
 * flag :true显示  :false隐藏
 * 如果用户引导程序未结束，显示新建站点
 * **/
export function showNewMap(flag=false){
    if(flag){
        store.commit("switchEditMapBtns", true);
    }else{
        store.commit("switchEditMapBtns", false);
    }
}

/**
 * 显示继续和关闭建图按钮
 * flag :true显示  :false隐藏
 * 如果用户引导程序未结束，显示新建站点
 * **/
export function showMappingBtn(flag=false){
    if(flag){
        store.commit("switchMappingBtns", true);
    }else{
        store.commit("switchMappingBtns", false);
    }
}

/**
 * 点击新建地图
 * seq=1
 * frame_id=salm
 * **/
export function newMapping() {
    Loading.service({
        text: "加载中..."
    });

    //发布启动建图话题,开始打点
    let message = {
        seq: 1,
        frame_id: 'slam'
    }
    var startMessage = new ROSLIB.Message(message);
    OBJ.topic['/task_switch'].publish(startMessage)
    
    console.log('[INFO]Start mapping');
    Loading.service().close();
};

/**
 * 点击继续建图
 * seq=2
 * frame_id=salm
 * **/
export function continueMapping() {
    if (store.state.rosMode === PARAMS.rosMode.gmapping) {
        Notification({
            title: "警告",
            message: "当前状态为建图状态，请点击保存",
            type: 'warning',
            position: 'top-left',
            offset: 100
        });
        return;
    }
    
    Loading.service({
        text: "加载中..."
    })

    //发布启动建图话题,开始打点
    let goonMsg = {
        seq: 2,
        frame_id: 'slam'
    }
    var goonMessage = new ROSLIB.Message(goonMsg)
    OBJ.topic['/task_switch'].publish(goonMessage);
    console.log('[INFO]continue mapping');
    Loading.service().close();
};

/**
 * 保存并继续建图
 * **/
export function submitSaveMapContinue(mapName){
    Loading.service({
        text: "保存中..."
    });
    //保存地图
    let saveMsg = {
        seq: 1,
        frame_id: `save_map_${mapName}`
    }

    OBJ.topic['/task_switch'].publish(new ROSLIB.Message(saveMsg));
    setTimeout(function(){
        Loading.service().close();
        Notification({
            title: "成功",
            message: "保存成功",
            type: 'success',
            position: 'top-left',
            offset: 100
        });
    }, 2000)
};

/**
 * 保存并关闭建图
 * 保存：seq=1,frame_id=map
 * 关闭建图:seq=0,frame_id=slam
 * **/
export function submitSaveMapBtn(mapName) {
    Loading.service({
        text: "保存中..."
    });
    //保存地图
    let saveMsg = {
        seq: 1,
        frame_id: `save_map_${mapName}`
    }
    //关闭建图
    let closeMsg = {
        seq: 0,
        frame_id: 'slam'
    }
    OBJ.topic['/task_switch'].publish(new ROSLIB.Message(saveMsg));

    //关闭建图
    setTimeout(function () {
        var closeMessage = new ROSLIB.Message(closeMsg);
        OBJ.topic['/task_switch'].publish(closeMessage);
        setTimeout(function () {
            //关闭建图模态框
            showNewMap(false);

            Loading.service().close();

            Notification({
                title: "成功",
                message: "保存成功",
                type: 'success',
                position: 'top-left',
                offset: 100
            });
        }, 8000)
    }, 1000)
};


