<template>
  <svg :class="svgClass" aria-hidden="true">
    <use :xlink:href="iconName" class="useIcon"></use>
    <use :xlink:href="iconName2" class="useIcon2"></use>
  </svg>
</template>

<script>
export default {
  name: 'svg-icon',
  props: {
    iconClass: {
      type: String,
      required: true
    },
    className: {
      type: String
    }
  },
  data(){
    return{
    }
  },
  computed: {
    iconName() {
      return `#icon-${this.iconClass}`
    },
    iconName2() {
      return `#icon-${this.iconClass}2`
    },
    svgClass() {
      if (this.className) {
        return 'svg-icon ' + this.className
      } else {
        return 'svg-icon'
      }
    }
  }
}
</script>

<style scoped>

.svg-icon {
  width: 1.5em;
  height: 1.5em;
  vertical-align: -0.4em;
  overflow: hidden;
}
</style>



