import vue from '../main'
import store from '../store/index';
import { Notification } from 'element-ui';
import { OBJ, viewer } from './init'
import { debounce } from './methods'
var container
/**
 * 绘制robot
 * 放大缩小时,地图加载时
 * 移除重复的
 * **/
export function robotMarker() {
    return new Promise(function (resolve, reject) {
        if (OBJ.robotStage) {
            viewer.scene.removeChild(OBJ.robotStage);
        }
        // marker for the robot
        var robotMarker = new ROS2D.NavigationArrow({
            size: 20,
            strokeSize: 1,
            fillColor: createjs.Graphics.getRGB(255, 255, 153, 0.66),
            pulse: true
        });
        // wait for a pose to come in first
        robotMarker.visible = false;
        viewer.scene.addChild(robotMarker);
        var initScaleSet = false;

        // setup a listener for the robot pose
        OBJ.topic['/robot_pose'].subscribe(function (pose) {
            OBJ.robotPose = pose;
            // update the robots position on the map
            robotMarker.x = pose.position.x;
            robotMarker.y = -pose.position.y;
            if (!initScaleSet) {
                robotMarker.scaleX = 1.0 / viewer.scene.scaleX;
                robotMarker.scaleY = 1.0 / viewer.scene.scaleY;
                initScaleSet = true;
            }
            // change the angle
            robotMarker.rotation = viewer.scene.rosQuaternionToGlobalTheta(pose.orientation);
            robotMarker.visible = true;
            OBJ.robotStage = robotMarker;
            resolve()
        });
    })
}
//robot定位
export function locationRobot() {
    // zoomview.startZoom(OBJ.imgMapStage.currentImage.x + OBJ.robotStage.x, OBJ.imgMapStage.currentImage.y - OBJ.robotStage.y);
    viewer.scene.x = OBJ.mapXY.x + OBJ.robotStage.x;
    viewer.scene.y = OBJ.mapXY.y + OBJ.robotStage.y;
}
export function initPoseFn(flag){
    let startPose = null;
    let endPose = null;
    let rotation = null;
    let ori = null;
    let mouseClickFlag = false;
    let mouseMoveFlag = false;
    var initFlag = true
    viewer.scene.addEventListener('stagemousedown', function(event) {
        if(!initFlag){return}
        if(!mouseClickFlag){
            startPose = viewer.scene.globalToRos(event.stageX, event.stageY);
            mouseClickFlag = true;
        }
        if(mouseMoveFlag && mouseClickFlag){
            if(flag == 'initPose'){
                var covariance = [];
                for (var i = 0; i < 36; i++){
                    covariance[i] = 0;
                }
                covariance[0] = 0.25;
                covariance[7] = 0.25;
                covariance[35] = Math.pow((Math.PI/12), 2);
                let poseMsg =new ROSLIB.Message({
                    header:{
                        seq: 0,
                        frame_id: "map"
                    },
                    pose:{
                        pose:{
                            position: startPose,
                            orientation:ori
                        },
                        covariance
                    }
                })
                OBJ.topic['/initialpose'].publish(poseMsg)
            }else{
                let poseMsg =new ROSLIB.Message({
                    header:{
                        seq: 2,
                        stamp:{},
                        frame_id: "map"
                    },
                    pose:{
                        position: startPose,
                        orientation:ori
                    }
                })
                OBJ.topic['/move_base_simple/goal'].publish(poseMsg)
            }
            viewer.scene.removeChild(OBJ.initialPoseStage);
            viewer.scene.removeEventListener('stagemousemove',{})
            initFlag = false;
        }
    })
    viewer.scene.addEventListener('stagemousemove', function(event) {
        if(mouseClickFlag && initFlag){
            if (OBJ.initialPoseStage) {
                viewer.scene.removeChild(OBJ.initialPoseStage);
            }
            endPose = viewer.scene.globalToRos(event.stageX, event.stageY);
            ori = orientationFn(startPose, endPose);
            rotation = viewer.scene.rosQuaternionToGlobalTheta(ori);
            OBJ.initialPoseStage = initPoseIcon({});
            viewer.scene.addChild(OBJ.initialPoseStage);
            OBJ.initialPoseStage.x = startPose.x;
            OBJ.initialPoseStage.y = -startPose.y;
            OBJ.initialPoseStage.rotation = rotation;
            mouseMoveFlag = true;
        }
    })
}

function initPoseIcon(options) {
    var size = options.size || 0.5;
    var strokeSize = options.strokeSize || 2;
    var strokeColor = options.strokeColor || '#3b6dde'; //e8eaf6
    var fillColor = options.fillColor || '#ffe83b';
    var fillColorExt = options.fillColorExt || createjs.Graphics.getRGB(0,150,136,0.7);
    var graphics = new createjs.Graphics();
    graphics.beginFill(fillColorExt);  
    graphics.drawCircle(size*2,size*2,size*1.5);  
    graphics.endFill();
    graphics.beginFill(fillColor);  
    graphics.drawCircle(size*2,size*2,size/1.2);  
    graphics.endFill();
    graphics.setStrokeStyle(strokeSize);
    graphics.beginFill(fillColor);
    graphics.moveTo(size*2, size*0.6);
    graphics.lineTo(size*1.3, size*2);
    graphics.lineTo(size*2.7, size*2);
    graphics.closePath()
    graphics.endFill();
    graphics.endStroke();
    return iconWrapper(graphics, {
        regX: size * 2,
        regY: size * 2,
        rotation: 90,
        scaleX: 0.25,
        scaleY: 0.25,
    });
}

function iconWrapper(iconGraphics, options)
{
    var size = options.size || 10;
    var reg = {
        x: options.regX || 0,
        y: options.regY || 0,
        rotation: options.rotation || 0
    };
    var scale = {
        x: options.scaleX || 1,
        y: options.scaleY || 1
    };
    var container = new createjs.Container();
    var shape = new createjs.Shape(iconGraphics);
    shape.regX = reg.x;
    shape.regY = reg.y;
    shape.rotation = reg.rotation;
    container.addChild(shape);
    return container;
}
let orientationFn = function(startPose, endPose){
    let xDelta = startPose.x - endPose.x;
    let yDelta = startPose.y - endPose.y;
    let thetaRadians = Math.atan2(xDelta, yDelta);
    if (thetaRadians >= 0 && thetaRadians <= Math.PI) {
        thetaRadians += (3 * Math.PI / 2);
    } else {
        thetaRadians -= (Math.PI / 2);
    }
    thetaRadians += Math.PI;
    let qz = Math.sin(-thetaRadians / 2.0);
    let qw = Math.cos(-thetaRadians / 2.0);
    let orientation = new ROSLIB.Quaternion({ x: 0, y: 0, z: qz, w: qw });
    return orientation;
}



