import Vue from 'vue'
import Router from 'vue-router'
const Home = resolve => {
  resolve(require('@/components/home'))
}
const Collection = resolve => {
  resolve(require('@/components/collection/collection.vue'))
}

Vue.use(Router)
const router = new Router({
  // mode: 'history',
  mode: 'hash',
  routes:[
    {
      path :'*',//404匹配未找到的页面
      component : Home
    },{
      path: '/home',
      name:"home",
      component: Home,
      meta:{
        keepAlive:false
      }
    },{
      path: '/collection',
      name: "collection",
      component: Collection,
    }
  ]
})
export default router



