<template>
    <div class="flex">
        <el-drawer
        title="设置权限"
        :visible.sync="drawer"
        :close-on-press-escape="false"
        custom-class="authorBox"
        :wrapperClosable="closable"
        :show-close="showClose"
        :direction="direction"
        @close="closeDrawer">

            <div class="authorPsw flex">
                <el-input v-model="authorPassword" show-password placeholder="请输入授权密码" @keyup.enter.native="submitForm"></el-input>
                <el-button  type="primary" @click="submitForm('ruleForm')">提交</el-button>
            </div>
        </el-drawer>
        
    </div>
</template>

<script>
import { submitAuthor, showAuthor} from '@/js/methods'
export default {
    name: 'authorization',
    components:{
        
    },
    data(){
        return{
            drawer: false,
            direction: 'btt',
            closable: true,
            showClose: false,
            authorPassword:'',
        }
    },

    mounted(){
        
    },

    methods:{
        //提交
        submitForm() {
            const that = this;
            submitAuthor(this.authorPassword);
       },

       //关闭模态框
        closeDrawer: function(){
            showAuthor(false)
        }
    },

    //属性监听
    watch:{
       "$store.state.authorization": function(val){
           this.drawer = val;
       }
    }

}
</script>

<style scoped lang="less">
.authorPsw{
    padding: 0 15px;
    button{
        margin-left: 20px;
    }
}

</style>


