<template>
    <div class="ws_topic_config">
        <el-button size="mini" type="primary" round @click="dialogTableVisible = true">配置topic</el-button>
        <el-dialog title="服务器 Topic 订阅/发布" :visible.sync="dialogTableVisible">
        <el-table  :data="topicList" height="500">
            <el-table-column property="name" label="TopicName"></el-table-column>
            <el-table-column
                fixed="right"
                label="Operation"
                :render-header="renderHeader">
                
                <template slot-scope="scope" >
                    <el-radio-group v-model="scope.row.subFlag" @change="((val) => {radioChange(val, scope.$index)})" size="small"
                        v-if="scope.row.name == '/robot_pose' || 
                        scope.row.name == '/task_state' || 
                        scope.row.name == '/battery' || 
                        scope.row.name == '/task_switch' || 
                        scope.row.name == '/diagnostics_agg' || 
                        scope.row.name == '/behavior_list' || 
                        scope.row.name == '/behavior_updated'">
                        <el-radio-button disabled label="sub"></el-radio-button>
                        <el-radio-button disabled label="pub"></el-radio-button>
                        <el-radio-button disabled label="Nan"></el-radio-button>
                    </el-radio-group>
                    <el-radio-group v-else v-model="scope.row.subFlag" @change="((val) => {radioChange(val, scope.$index)})" size="small">
                        <el-radio-button label="sub"></el-radio-button>
                        <el-radio-button label="pub"></el-radio-button>
                        <el-radio-button label="Nan"></el-radio-button>
                    </el-radio-group>
                </template>
            </el-table-column>
            </el-table>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: 'wsTopicConfig',
    data(){
        return {
            dialogTableVisible: false,
            topicList: [{
                name: '/map_ostream',
                type: 'scheduling_msgs/MapStream',
                subFlag: 'Nan'
                }, {
                name: '/map_ostream_path',
                type: 'scheduling_msgs/MapStream',
                subFlag: 'Nan'
                }, {
                name: '/map_ostream_display',
                type: 'scheduling_msgs/MapStream',
                subFlag: 'Nan'
                }, {
                name: '/map_ostream_reflection',
                type: 'scheduling_msgs/MapStream',
                subFlag: 'Nan'
                }, {
                name: '/map_istream',
                type: 'scheduling_msgs/MapStream',
                subFlag: 'Nan'
                }, {
                name: '/map_istream_path',
                type: 'scheduling_msgs/MapStream',
                subFlag: 'Nan'
                }, {
                name: '/map_istream_display',
                type: 'scheduling_msgs/MapStream',
                subFlag: 'Nan'
                }, {
                name: '/map_istream_reflection',
                type: 'scheduling_msgs/MapStream',
                subFlag: 'Nan'
                }, {
                name: '/robot_pose',
                type: 'geometry_msgs/Pose',
                subFlag: 'sub'
                }, {
                name: '/initialpose',
                type: 'geometry_msgs/PoseWithCovarianceStamped',
                subFlag: 'pub'
                }, {
                name: '/move_base/GlobalPlanner/plan',
                type: 'nav_msgs/Path',
                subFlag: 'sub'
                }, {
                name: '/task_state',
                type: 'diagnostic_msgs/DiagnosticStatus',
                subFlag: 'sub'
                }, {
                name: '/battery',
                type: 'sensor_msgs/BatteryState',
                subFlag: 'sub'
                }, {
                name: '/scan_rectified',
                type: 'sensor_msgs/LaserScan',
                subFlag: 'sub'
                }, {
                name: '/move_base/global_costmap/footprint',
                type: 'geometry_msgs/PolygonStamped',
                subFlag: 'sub'
                }, {
                name: '/task_switch',
                type: 'std_msgs/Header',
                subFlag: 'sub'
                }, {
                name: '/diagnostics_agg',
                type: 'diagnostic_msgs/DiagnosticArray',
                subFlag: 'sub'
                }, {
                name: '/cmd_vel',
                type: 'geometry_msgs/Twist',
                subFlag: 'sub'
                }, {
                name: '/cmd_string',
                type: 'std_msgs/String',
                subFlag: 'sub'
                }, {
                name: '/shell_string',
                type: 'std_msgs/String',
                subFlag: 'Nan'
                }, {
                name: '/move_base/goal',
                type: 'move_base_msgs/MoveBaseActionGoal',
                subFlag: 'pub'
                }, {
                name: '/move_base_simple/goal',
                type: 'geometry_msgs/PoseStamped',
                subFlag: 'pub'
                }, {
                name: '/move_base/result',
                type: 'move_base_msgs/MoveBaseActionResult',
                subFlag: 'sub'
                },  {
                name: '/amcl_pose',
                type: 'geometry_msgs/PoseWithCovarianceStamped',
                subFlag: 'sub'
                },  {
                name: '/flexbe/behavior_updated_hz',
                type: 'std_msgs/String',
                subFlag: 'sub'
                },  {
                name: '/flexbe/behavior_updated',
                type: 'std_msgs/String',
                subFlag: 'sub'
                },  {
                name: '/flexbe/behavior_list',
                type: 'std_msgs/String',
                subFlag: 'Nan'
                },  {
                name: '/flexbe/execute_behavior/goal',
                type: 'flexbe_msgs/BehaviorExecutionActionGoal',
                subFlag: 'pub'
                },  {
                name: '/flexbe/command/preempt',
                type: 'std_msgs/Empty',
                subFlag: 'pub'
                }, {
                name: '/flexbe/command/pause',
                type: 'std_msgs/Bool',
                subFlag: 'pub'
                }, {
                name: '/map',
                type: 'nav_msgs/OccupancyGrid',
                subFlag: 'Nan'
                }]
        }
    },
    mounted(){
        let list = window.localStorage.getItem('wsTopicConfig')
        if(list){
            this.topicList = JSON.parse(list)
        }else{
            window.localStorage.setItem('wsTopicConfig', JSON.stringify(this.topicList))
        }
    },
    methods:{
        radioChange(val, index){
            this.topicList[index].subFlag = val
        },
        renderHeader(h, {column}){
            let a = [
                h('span', column.label),
                h('el-button',{
                props: {
                    size: 'small',
                    type: "primary"
                }, 
                style:'margin-left:33px;',
                on: {
                    click: () => {
                        console.log(this.topicList)
                        window.localStorage.setItem('wsTopicConfig', JSON.stringify(this.topicList))
                        this.dialogTableVisible = false;
                    }
                }
                }, 'save')]
            return h('div', a)
        }
    }
}
</script>
<style>
</style>


