<template>
    <div class="flex">
        <el-drawer
        title="新建分支"
        :visible.sync="drawer"
        :close-on-press-escape="false"
        custom-class="authorBox"
        :wrapperClosable="closable"
        :show-close="showClose"
        :direction="direction"
        @close="closeDrawer">

            <div class="authorPsw flex">
                <el-input v-model="branchName" placeholder="请输入分支名称" @keyup.enter.native="submitBranch"></el-input>
                <el-button  type="primary" @click="submitBranch">提交</el-button>
            </div>
        </el-drawer>
        
    </div>
</template>

<script>
import { newBranch} from '@/js/branch'
export default {
    name: 'addBranch',
    components:{
        
    },
    data(){
        return{
            drawer: false,
            direction: 'btt',
            closable: true,
            showClose: false,
            branchName:'',
        }
    },

    mounted(){
        
    },

    methods:{
        //提交
        submitBranch() {
            const that = this;
            newBranch(this.branchName);
       },

       //关闭模态框
        closeDrawer: function(){
            this.$store.commit("switchBranch", false);
        }
    },

    //属性监听
    watch:{
       "$store.state.showBranch": function(val){
           this.drawer = val;
       }      
    }

}
</script>

<style scoped lang="less">
.authorPsw{
    padding: 0 15px;
    button{
        margin-left: 20px;
    }
}

</style>


