<template>
    <div class="fblists">
        <div v-if="flexTag == 'base'">
            <div class="searchBox">
                <el-input placeholder="请输入任务名称" v-model="inputFlexBEList" class="input-with-select" @input="baseInputChange">
                </el-input>
            </div>
            <div class="setItem flex" v-for="(item,index) in baseFlexBeList" :key="index">
                <div class="title" @click="goTask(item.name)">
                    <i class="el-icon-guide flexTaskName"></i>
                    {{item.name}}<small><span>{{item.tag}}</span></small>
                    </div>
                <div class="rightIcon" v-show="develop">
                    <i class="el-icon-delete" @click="deleteTask(item.name)"></i>
                </div>
            </div>
        </div>
        <div v-else-if="flexTag == 'other'">
            <div class="searchBox">
                <el-input placeholder="请输入任务名称" v-model="inputFlexBEList" class="input-with-select" @input="otherInputChange">
                </el-input>
            </div>
            <div class="setItem flex" v-for="(item,index) in otherFlexBeList" :key="index">
                <div class="title" @click="goTask(item.name)">
                    <i class="el-icon-guide flexTaskName"></i>
                    {{item.name}}<small><span>{{item.tag}}</span></small>
                    </div>
                <div class="rightIcon" v-show="develop">
                    <i class="el-icon-delete" @click="deleteTask(item.name)"></i>
                </div>
            </div>
        </div>
    </div>    
</template>
<script>
import { MessageBox } from 'element-ui'
import { OBJ } from '../../js/init';
import { pubFlexbeBehaviorGoal, deleteFlexbeTask } from '../../js/flexbe';
import { debounce } from '../../js/methods'

export default {
    name: 'fblists',
    components:{},
    props: ['flexTag'],
    data(){
        return{
            baseFlexBeList: [],
            baseFlexBeLists:[],
            otherFlexBeList: [],
            otherFlexBeLists:[],
            inputFlexBEList: "",
            develop: this.$store.state.userRole === "developer" ? true : false,
        }
    },
    //计算属性
    watch:{       
        '$store.state.flexBeList':function(val){
            this.baseFlexBeList = []
            this.baseFlexBeLists = []
            this.otherFlexBeList = []
            this.otherFlexBeLists = []
            for(var key in val){
                let valu = val[key]
                let str = valu.indexOf('\:');
                let tag = valu.substring(str+1,valu.length)
                let tagName = valu.substring(0,str)
                if(tagName == 'base'){
                    this.baseFlexBeList.push({'name': key, 'tag': tag})
                    this.baseFlexBeLists.push({'name': key, 'tag': tag})
                }else{
                    this.otherFlexBeList.push({'name': key, 'tag': tag})
                    this.otherFlexBeLists.push({'name': key, 'tag': tag})
                }
            }
        },
        '$store.state.userRole':function(val){
          if(val === "developer"){
              this.develop = true;
          }else {
              this.develop = false;
          }
        }
    },

    mounted(){
    },

    methods:{
        goTask(name){
            var flexbeStatus = window.localStorage.getItem('flexBeTaskStatus')
            if(flexbeStatus != 'behavior_exit'){
                this.$confirm('此操作将终止执行中的任务, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    center: true,
                    type: 'warning'
                }).then(() => {
                    pubFlexbeBehaviorGoal(name)
                    this.$emit("transferSetting", "flexbeGoal")
                }).catch(() => {
                    this.$notify({
                        message: '已取消',
                        position: 'top-left',
                        type: "success",
                        offset: 100,
                        duration: 1000
                    });    
                });
            }else{
                this.$confirm(`是否下发 ${name} 任务?`, '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    center: true,
                    type: 'info'
                }).then(() => {
                    pubFlexbeBehaviorGoal(name)
                    this.$emit("transferSetting", "flexbeGoal")
                }).catch(() => {
                    this.$notify({
                        message: '已取消',
                        position: 'top-left',
                        type: "success",
                        offset: 100,
                        duration: 1000
                    });            
                });
            }
        },
        deleteTask(name){
            this.$confirm('是否删除此任务？', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    center: true,
                    type: 'warning'
                }).then(() => {
                    deleteFlexbeTask(name)
                }).catch(() => {
                    this.$notify({
                        message: '已取消操作',
                        position: 'top-left',
                        type: "success",
                        offset: 100,
                        duration: 1000
                    });    
                });
        },
        //搜索
        baseInputChange: debounce(function(tag){
            let that = this;
            if(that.inputFlexBEList){
                that.baseFlexBeList = [];
                for(var i in that.baseFlexBeLists){
                    if(that.baseFlexBeLists[i].name.indexOf(that.inputFlexBEList) >= 0){
                        that.baseFlexBeList.push(that.baseFlexBeLists[i])
                    }
                }
            }else{
                that.baseFlexBeList = that.baseFlexBeLists;
            }
        }, 500),//搜索
        otherInputChange: debounce(function(tag){
            let that = this;
            if(that.inputFlexBEList){
                that.otherFlexBeList = [];
                for(var i in that.otherFlexBeLists){
                    if(that.otherFlexBeLists[i].name.indexOf(that.inputFlexBEList) >= 0){
                        that.otherFlexBeList.push(that.otherFlexBeLists[i])
                    }
                }
            }else{
                that.otherFlexBeList = that.otherFlexBeLists;
            }
        }, 500),
    }


}
</script>

<style scoped lang="less">
.setItem{
    background-color: #ffffff;
    justify-content: space-between;
    height: 48px;
    line-height: 48px;
    margin-bottom: 3px;
    font-size: 16px;
    padding: 0 10px;
    .title{
        flex: 8;
        text-align: left;
    }
    small{
        color: LightGrey;
    }
    .tag{
        color: LightGrey;
        height: 48px;
        line-height: 48px;
        font-size: 13px;
        resize: none;
    }
    .rightIcon{
        flex: 0.3;
        cursor: pointer;
    }
}
.fblists{
        position: relative;
        font-size: 14px;
        height: 100%;
        font-size: 16px;
        background-color: #eeeeee;
        .flexTaskName{
            color: #409EFF;
            padding: 15px;
        }
        .searchBox{
            padding: 15px;
            background-color: #ffffff;
        }
        .flex{
            .title{
                position: relative;
                height: 100%;
            span{
                display: block;
                position: absolute;
                right: 30px;
                top: 0;
                width: 100px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow:ellipsis;
            }
                
            }
        }
    }
</style>


